<template>
  <transition name="alert-fade">
    <div
      v-if="show"
      class="vu-message alert-has-icon"
      :class="classes">
      <span v-if="colored" class="icon fonticon"></span>
      <span class="alert-message-wrap">
        <slot>
          <div v-html="text"></div>
        </slot>
      </span>
      <span v-if="closable" class="close fonticon fonticon-cancel" @click="$emit('update:show', false)"></span>
    </div>
  </transition>
</template>

<script>

import Showable from '../../mixins/showable';
import Colorable from '../../mixins/colorable';

export default {
  name: 'vu-message',
  mixins: [Showable, Colorable],
  props: {
    text: {
      type: String,
      default: () => '',
    },
    closable: {
      type: Boolean,
      default: () => true,
    },
    color: {
      type: String,
      default: () => 'primary',
    },
    animate: {
      type: Boolean,
      default: () => true,
    },
    timeout: {
      type: Number,
      default: () => 0,
    },
  },
  data: () => ({
    activeTimeout: 0,
    in: true,
  }),
  computed: {
    colored() {
      return !!this.color;
    },
    classes() {
      return [`alert-${this.color}`, {
        'alert-closable': this.closable,
        'alert-has-icon': !!this.icon,
      }];
    },
  },
  watch: {
    show: {
      immediate: true,
      handler() { this.setTimeout(); },
    },
  },
  methods: {
    setTimeout() {
      if (this.show && this.timeout) {
        window.clearTimeout(this.activeTimeout);
        this.activeTimeout = window.setTimeout(() => {
          this.$emit('update:show', false);
        }, this.timeout);
      }
    },
  },
};
</script>
<style scoped lang="scss">
.vu-message {
  display: flex;
  flex: 1 0 auto;
  word-break: break-word;
  margin: 5px auto;
  padding: 15px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .3);

  &.alert-has-icon {
    padding-left: 0px;
  }

  h4 {
    margin-top: 0;
    color: inherit;
  }

  > p, > ul {
    margin-bottom: 0;
  }

  > p + p {
    margin-top: 5px;
  }

  .icon, .close {
    flex: 0 0 auto;
  }

  .alert-message-wrap {
    flex: 1 1 auto;
  }
}

.alert-has-icon .icon,
.alert-closable .close {
  position: relative;
}

.alert-has-icon.alert-closable {
  padding: 0px;

  .icon, .alert-message-wrap {
    padding-top: 15px;
    padding-bottom: 15px;
  }
}

.alert-fade-enter, .alert-fade-leave-to {
  opacity: 0;
}

.alert-fade-enter-active, .alert-fade-leave-active {
  transition: all .5s ease-in-out;
}

.close {
  padding: 9px 12px;
  margin: 0;
  align-self: center;
}

</style>
