<template>
  <div>
    <span @click="changeStatus()" style="position: relative" v-click-outside="function () { if(!persistant) { changeStatus(false) } }">
      <slot />
      <vu-tooltip :attach="attach" v-if="innerShow" :open="innerShow" @click.stop :type="type" :side="side">
        <slot name="body" />
      </vu-tooltip>
    </span>
  </div>
</template>
<script>
import clickoutside from '../directives/v-click-outside';

/* eslint-disable vue/no-reserved-keys */
export default {
  name: 'vu-popover',
  directives: {
    'click-outside': clickoutside,
  },
  props: {
    persistant: {
      type: Boolean,
      default: () => true,
    },
    type: {
      type: String,
      default: () => 'popover',
    },
    side: {
      type: String,
      default: () => 'top',
    },
    show: {
      type: Boolean,
      default: () => false,
    },
    attach: {
      default: () => false,
    },
  },
  watch: {
    show(value) {
      this.innerShow = value;
    },
  },
  data() {
    return {
      innerShow: false,
    };
  },
  methods: {
    changeStatus(status) {
      this.innerShow = status === undefined ? !this.innerShow : status;
      this.$emit('update:show', this.innerShow);
    },
  },
};
</script>
<style scoped>
  .popover {
    color: #3D3D3D;
  }
</style>
