<template>
  <div class="form-group"
    :class="classes">
    <label class="control-label" v-if="label.length"
      >{{ label }}<span v-if="required" class="label-field-required"> *</span>
    /></label>
    <div :class="['vu-slider', { disabled }]">
      <div @mouseup="commit" class="vu-slider__container">
        <div ref="leftLabel" class="vu-slider__left vu-slider__label"
        >{{ showLabels ? labels.min : min }}</div>
        <div ref="rightLabel" class="vu-slider__right vu-slider__label"
        >{{ showLabels ? labels.max : max }}</div>
        <input
          class="slider vu-slider__left"
          type="range"
          :disabled="disabled"
          :value="innerValue"
          :min="min"
          :max="max"
          :step="step"
          :style="!labelsBeneath ? computedStyle : {}"
          @input="update(parseFloat($event.target.value))"
        />
        <div class="vu-slider__grey-bar"
          :style="{ left: labelsMargin, right: labelsMargin }">
          <div class="vu-slider__blue-bar vu-slider__blue-bar--left"
            :style="innerBlueBarStyle" />
        </div>
      </div>
          <!-- :style="(!labelsBeneath) ? { right: computedStyles.right } : {}" -->
      <div v-if="stepped" class="vu-slider__steps">
        <template v-for="(step, index) in steps">
          <div class="vu-slider__step" :key="index" :style="step.style"></div>
        </template>
      </div>
    </div>
    <span
      v-for="(error, pos) in errorBucket"
      :key="`${pos}-error-${error}`"
      style="display: block"
      class="form-control-error-text"
    >
      {{ error }}
    </span>
    <span v-if="helper.length" class="form-control-helper-text">{{ helper }}</span>
  </div>
</template>

<script>
import inputable from '../mixins/inputable';
import validatable from '../mixins/validatable';
import disablable from '../mixins/disablable';

import { RegistrableInput } from '../mixins/registrable';

export default {
  name: 'vu-slider',
  mixins: [inputable, disablable, validatable, RegistrableInput],
  props: {
    labels: {
      required: false,
      type: Object,
      default: () => ({
        min: 'Min',
        max: 'Max',
      }),
    },
    min: {
      default: 0,
    },
    max: {
      default: 10,
    },
    step: {
      type: Number,
      default: 1,
    },
    stepped: {
      type: Boolean,
      default: false,
    },
    showLabels: {
      type: Boolean,
      default: false,
    },
    labelsBeneath: {
      type: Boolean,
      default: false,
    },
  },
  data: () => ({
    labelsWidth: 0,
    innerValue: 0,
  }),
  created() {
    this.innerValue = this.value;
  },
  mounted() {
    const { leftLabel: { offsetWidth: leftWidth = 0 } = {}, rightLabel: { offsetWidth: rightWidth = 0 } = {} } = this.$refs;
    this.labelsWidth = Math.max(leftWidth, rightWidth);
  },
  computed: {
    steps() {
      return [];
    },
    labelsMargin() {
      return !this.labelsBeneath ? `${this.labelsWidth}px` : '';
    },
    computedStyle() {
      return {
        left: this.labelsMargin,
        right: this.labelsMargin,
        width: `calc(100% - ${ 2 * this.labelsWidth }px + 14px)`,
      };
    },
    innerBlueBarStyle() {
      const percent = ((this.innerValue - this.min) / (this.max - this.min)) * 100;
      return {
        // right: `calc(${percent}%${ left ? (` + ${ left }`) : ''})`,
        width: `${percent}%`,
      };
    },
  },
  methods: {
    commit() {
      if (this.disabled) {
        return;
      }
      this.$emit('mouseUp', this.value);
    },
    update(value) {
      if (this.disabled) { return; }
      this.innerValue = value;
      this.$emit('input', this.innerValue);
    },
  },
};
</script>

<style scoped lang="scss">
@import "../styles/_variables.scss";

.vu-slider {
  .vu-slider__label {
    height: 38px;
    padding: 9px 0.7em 0 0.7em;
  }

  .vu-slider__left,
  .vu-slider__right {
    position: absolute;
  }

  .vu-slider__left {
    left: 0;
  }

  .vu-slider__right {
    right: 0;
  }

  &,
  &__container {
    position: relative;
    height: 38px;
  }

  &__grey-bar,
  &__blue-bar {
    z-index: 1;
    position: absolute;
    height: 8px;
    background: linear-gradient($grey-2, $grey-2) no-repeat center;
    background-size: 100% 8px;
    z-index: 0;
    box-sizing: border-box;
  }

  &__grey-bar {
    border: solid 1px $grey-3;
    border-radius: 20px;
    margin: 15px auto;
    left: 0;
    right: 0;
  }

  &__blue-bar {
    left: 0;
    background: linear-gradient($blue-2, $blue-2) no-repeat center;
    background-size: 100% 8px;
    border: solid 1px $blue-3;
    border-style: solid none solid none;
    margin-top: -1px;
  }

  &__blue-bar--left {
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
  }

  input {
    height: 38px;
    appearance: none;
    margin: 0;
    /* Add some L/R padding to ensure box shadow of handle is shown */
    overflow: hidden;
    border: 0;
    border-radius: 1px;
    outline: none;
    background: linear-gradient(transparent, transparent) no-repeat center;
    /* Use a linear gradient to generate only the 2px height background */
    background-size: calc(100% - 4px) 2px;
    pointer-events: none;
    z-index: 1;
    position: absolute;
    margin-left: -7px;
    padding-right: 7px;

    &:active,
    &:focus {
      outline: none;
    }

    &::-moz-range-thumb {
      height: 20px;
      width: 20px;
      border-radius: 20px;
      margin: 7px;
      background-color: $grey-2;
      border: solid 1px $grey-5;
      position: relative;
      /* Add some margin to ensure box shadow is shown */
      cursor: pointer;
      appearance: none;
      pointer-events: all;
    }

    &::-webkit-slider-thumb {
      height: 20px;
      width: 20px;
      border-radius: 20px;
      margin: 7px;
      background-color: $grey-2;
      border: solid 1px $grey-5;
      position: relative;
      /* Add some margin to ensure box shadow is shown */
      cursor: pointer;
      appearance: none;
      pointer-events: all;
    }
  }
}

/** HOVER STATE */
.vu-slider:not(.disabled) input:hover {
  &::-webkit-slider-thumb,
  &::-moz-range-thumb,
   {
    background-color: $grey-3;
    border-color: $grey-6;
  }
  & + .vu-slider__grey-bar .vu-slider__blue-bar {
    background: linear-gradient($blue-3, $blue-3) no-repeat center;
    border-color: $blue-4;
  }

}

/** ACTIVE STATE */
.vu-slider:not(.disabled):active {
  input[type="range"] {
    z-index: 2;
  }
}
.vu-slider:not(.disabled) input[type="range"]:active {
  z-index: 10000;

  &::-webkit-slider-thumb,
  &::-moz-range-thumb {
    background-color: $blue-5;
    border-color: $blue-4;
  }

  & + .vu-slider__grey-bar .vu-slider__blue-bar {
    background: linear-gradient($blue-4, $blue-4) no-repeat center;
    border-color: $blue-5;
  }
}

/** DISABLED STATE */
.vu-slider.disabled {
  cursor: default !important;

  .vu-slider__grey-bar {
    background: linear-gradient($grey-2, $grey-2) no-repeat center;
    border-color: $grey-3;
  }
  .vu-slider__blue-bar {
    background: linear-gradient($blue-0, $blue-0) no-repeat center;
