<template>
  <div :style="intersected ? '' : `min-height: ${height}`">
    <slot v-if="intersected" />
    <slot v-else name="placeholder" />
  </div>
</template>

<script>
/* eslint-disable vue/no-reserved-keys */
export default {
  name: 'vu-lazy',
  props: {
    height: {
      type: String,
      default: () => '10px',
    },
    options: {
      type: Object,
      default: () => ({}),
    },
  },
  data: () => ({
    observer: null,
    intersected: false,
  }),
  mounted() {
    if ('IntersectionObserver' in window) {
      this.observer = new IntersectionObserver((entries) => {
        const el = entries[0];
        if (el.isIntersecting) {
          this.intersected = true;
          this.observer.disconnect();
          this.$emit('intersect');
        }
      }, this.options);
      this.observer.observe(this.$el);
    } else {
      this.intersected = true;
    }
  },
  destroyed() {
    if ('IntersectionObserver' in window) {
      this.observer.disconnect();
    }
  },
};
</script>
