export default {
  props: {
    rules: {
      type: [Array],
      default: () => [() => true],
    },
    required: {
      type: Boolean,
      default: () => false,
    },
    success: {
      type: Boolean,
      default: () => false,
    },
  },
  data: () => ({
    errorBucket: [],
    valid: true,
  }),
  watch: {
    value(v) {
      this.valid = this.validate(v);
    },
  },
  computed: {
    classes() {
      return {
        'has-error': !this.valid,
        'has-success': this.success && this.valid,
      };
    },
    hasError() {
      return this.errorBucket.length > 0;
    },
    hasSuccess() {
      return this.errorBucket.length === 0;
    },
    isValid() {
      if (!this.required) return true;
      switch (typeof this.value) {
        case 'string':
        case 'array':
        case 'number':
        case 'date':
          return this.value.length !== 0;
        default:
          return true;
      }
    },
  },
  methods: {
    validate(value) {
      const errorBucket = [];
      let errorCount = 0;
      const v = value || this.value;

      for (let index = 0; index < this.rules.length; index++) {
        const rule = this.rules[index];
        const valid = typeof rule === 'function' ? rule(v) : rule;

        if (typeof valid === 'string') {
          errorBucket.push(valid);
          errorCount += 1;
        } else if (typeof valid === 'boolean' && !valid) {
          errorCount += 1;
        } else if (typeof valid !== 'boolean') {
          // eslint-disable-next-line no-console
          console.error(`Rules should return a string or boolean, received '${typeof valid}' instead`, this);
        }
      }

      this.errorBucket = errorBucket;
      this.valid = errorCount === 0 && this.isValid;

      return this.valid;
    },
  },
};
