const uuid = () => {
  if (window) {
    // eslint-disable-next-line no-bitwise, no-mixed-operators
    return ([1e7] + -1e3 + -4e3 + -8e3 + -1e11).replace(/[018]/g, (c) => (c ^ (window.crypto || window.msCrypto).getRandomValues(new Uint8Array(1))[0] & 15 >> c / 4).toString(16));
  }
  return crypto.uuid();
};

export default uuid;
