<template>
  <div class="form-group"
    :class="classes">
    <label class="control-label"
      v-if="label.length">{{ label }}<span v-if="required" class="label-field-required"> *</span></label>
    <div :class="['vu-range', { disabled }]">
      <div @mouseup="commit" class="vu-range__inputs-container">
        <input :disabled="disabled" @input="update('lower', parseFloat($event.target.value))" :value="lowervalue" :min="min" :max="max" :step="step" class="slider vu-range__left" type="range">
        <input :disabled="disabled" @input="update('upper', parseFloat($event.target.value))" :value="uppervalue" :min="min" :max="max" :step="step" class="slider vu-range__right" type="range">
        <div class="vu-range__grey-bar">
          <div class="vu-range__blue-bar" :style="computedStyles" />
        </div>
      </div>
      <div v-if="showLabels" class="vu-range__labels-container">
        <div class="vu-range__left vu-range__left-label">{{ minLabel }}</div>
        <div class="vu-range__right vu-range__right-label">{{ maxLabel }}</div>
        <div v-if="lowervalue !== min && uppervalue !== lowervalue"
          class="vu-range__lower-label"
          :style="'left: ' + ((lowervalue - min) / (max - min)) * 100 + '%'"
          >{{ lowerLabel }}</div>
        <div v-if="uppervalue !== max"
          class="vu-range__upper-label"
          :style="'left: ' + ((uppervalue - min) / (max - min)) * 100 + '%'"
          >{{ upperLabel }}</div>
      </div>
    </div>
    <span
      v-for="(error, pos) in errorBucket"
      :key="`${pos}-error-${error}`"
      style="display: block"
      class="form-control-error-text">
      {{ error }}
    </span>
    <span v-if="helper.length" class="form-control-helper-text">{{ helper }}</span>
  </div>
</template>

<script>
import inputable from '../mixins/inputable';
import validatable from '../mixins/validatable';
import rangeable from '../mixins/rangeable';
import disablable from '../mixins/disablable';

import { RegistrableInput } from '../mixins/registrable';

export default {
  name: 'vu-range',
  mixins: [inputable, rangeable, disablable, validatable, RegistrableInput],
  props: {
    step: {
      type: Number,
      default: 1,
    },
    showLabels: {
      type: Boolean,
      default: true,
    },
    customLabels: {
      type: Array,
      required: false,
    },
  },
  data() {
    return {
      lowervalue: Math.min(...this.value),
      uppervalue: Math.max(...this.value),
    };
  },
  watch: {
    value: {
      immediate: true,
      handler() {
        this.lowervalue = Math.min(...this.value);
        this.uppervalue = Math.max(...this.value);
      },
    },
  },
  computed: {
    minLabel() {
      if (this.customLabels && this.customLabels.length) {
        return this.customLabels[0];
      }
      return this.min;
    },
    maxLabel() {
      if (this.customLabels && this.customLabels.length) {
        return this.customLabels[((this.max + (this.max % this.step)) / this.step) - this.min];
      }
      return this.max;
    },
    lowerLabel() {
      if (this.customLabels && this.customLabels.length) {
        return this.customLabels[(this.lowervalue - this.min) / this.step];
      }
      return this.lowervalue;
    },
    upperLabel() {
      if (this.customLabels && this.customLabels.length) {
        return this.customLabels[(this.uppervalue - this.min) / this.step];
      }
      return this.uppervalue;
    },
    computedStyles() {
      const percent = ((this.lowervalue - this.min) / (this.max - this.min)) * 100;
      return {
        width: `${(((this.uppervalue - this.min) - (this.lowervalue - this.min)) / (this.max - this.min)) * 100}%`,
        left: `${percent}%`,
      };
    },
  },
  methods: {
    commit() {
      if (this.disabled) { return; }
      this.$emit('mouseup', [this.lowervalue, this.uppervalue]);
    },
    update(type, v) {
      if (this.disabled) { return; }
      let upper;
      let lower;
      if (type === 'lower') {
        lower = Math.min(v, this.uppervalue);
        upper = Math.max(v, this.uppervalue);
        // if (lower === this.max) lower = this.max - this.step;
        if (lower > upper) { upper = Math.min(upper + this.step, this.max); }
      } else {
        lower = Math.min(v, this.lowervalue);
        upper = Math.max(v, this.lowervalue);
        // if (upper === this.min) upper = this.min + this.step;
        if (lower > upper) { lower = Math.max(lower - this.step, this.min); }
      }
      this.lowervalue = lower;
      this.uppervalue = upper;

      this.$emit('input', [this.lowervalue, this.uppervalue]);
    },
  },
};
</script>

<style scoped lang="scss">
  @import '../styles/_variables.scss';

  .vu-range .vu-range__labels-container {
      margin: auto;
      position: relative;
      height: 20px;
