      .vu-range__left, .vu-range__right, .vu-range__lower-label, .vu-range__upper-label {
        position: absolute;
      }

      .vu-range__lower-label, .vu-range__upper-label {
        transform: translateX(-50%);
      }

      .vu-range__left {
        left: 0;
      }
      .vu-range__right   {
        right: 0;
      }
  }

  .vu-range .vu-range__inputs-container {
    position: relative;
    height: 38px;
    margin: 0 -7px;

    .vu-range__grey-bar, .vu-range__blue-bar {
      z-index: 1;
      position: absolute;
      height: 8px;
      width: 100%;
      background: linear-gradient($grey-2, $grey-2) no-repeat center;
      background-size: 100% 8px;
      z-index: 0;
      box-sizing: border-box;
    }

    .vu-range__grey-bar {
      margin: -1px;
      border: solid 1px $grey-3;
      border-radius: 20px;
      margin: 15px auto;
      width: calc(100% - 30px);
      left: 0;
      right: 0;
    }

    .vu-range__blue-bar {
      background: linear-gradient($blue-2, $blue-2) no-repeat center;
      background-size: 100% 8px;
      border: solid 1px $blue-3;
      border-style: solid none solid none;
      margin-top: -1px;
    }

    input[type=range] {
      height: 38px;
      appearance: none;
      margin: 0;
      /* Add some L/R padding to ensure box shadow of handle is shown */
      overflow: hidden;
      border: 0;
      border-radius: 1px;
      outline: none;
      background: linear-gradient(transparent, transparent) no-repeat center;
      /* Use a linear gradient to generate only the 2px height background */
      background-size: calc(100% - 4px) 2px;
      pointer-events: none;
      z-index: 1;
      position: absolute;
      padding-right: 14px;

      &:active,
      &:focus {
          outline: none;
      }

      &::-moz-range-thumb {
        height: 20px;
        width: 20px;
        border-radius: 20px;
        margin: 7px;
        background-color: $grey-2;
        border: solid 1px $grey-5;
        position: relative;
        /* Add some margin to ensure box shadow is shown */
        cursor: pointer;
        appearance: none;
        pointer-events: all;
      }

      &::-webkit-slider-thumb {
        height: 20px;
        width: 20px;
        border-radius: 20px;
        margin: 7px;
        background-color: $grey-2;
        border: solid 1px $grey-5;
        position: relative;
        /* Add some margin to ensure box shadow is shown */
        cursor: pointer;
        appearance: none;
        pointer-events: all;
      }
    }
  }

  /** HOVER STATE */
  .vu-range__inputs-container:hover {
    .vu-range__blue-bar {
      background: linear-gradient($blue-3, $blue-3) no-repeat center;
      border-color: $blue-4;
    }


    input[type=range] {
      &::-webkit-slider-thumb,
      &::-moz-range-thumb {
        background-color: $grey-3;
        border-color: $grey-6;
      }
    }
  }

  /** ACTIVE STATE */
  .vu-range:active .vu-range__inputs-container {
    .vu-range__blue-bar {
      background: linear-gradient($blue-4, $blue-4) no-repeat center;
      border-color: $blue-5;
    }
    input[type=range] {
      z-index: 2;
    }
  }
  .vu-range .vu-range__inputs-container input[type=range]:active {
    z-index: 10000;

    input[type=range] {
      &::-webkit-slider-thumb,
      &::-moz-range-thumb {
        background-color: $blue-5;
        border-color: $blue-4;
      }
    }
  }

  /** DISABLED STATE */
  .vu-range.disabled .vu-range__inputs-container {
    cursor: default !important;

    .vu-range__grey-bar {
      background: linear-gradient($grey-2, $grey-2) no-repeat center;
      border-color: $grey-3;
    }
    .vu-range__blue-bar {
      background: linear-gradient($blue-0, $blue-0) no-repeat center;
      border-color: $grey-3;
    }

    input[type=range] {
      &::-webkit-slider-thumb,
      &::-moz-range-thumb {
        background-color: $grey-2;
        border-color: $grey-3;
        cursor: default !important;
      }
    }
  }
</style>
