<template>
  <div class="form-group"
    :class="classes">
    <label class="control-label"
      v-if="label.length">{{ label }}<span v-if="required" class="label-field-required"> *</span></label>
    <div v-click-outside="function () { open = false; search = ''; }"
      :class="['select',
        'select-autocomplete', {
        'dropdown-visible': open,
        'select-disabled': disabled
      }]">
      <div @click="open = true; $refs.input.focus()"
        class="autocomplete-searchbox"
        :class="{
           'autocomplete-searchbox-active': open,
           'disabled': disabled,
          }">
        <span v-for="v in value"
          :key="`${_uid}-tag-${v}`"
          class="badge-default badge badge-root badge-selectable badge-closable"
          style="outline: 0px;">
          <span class="badge-content">{{ getOption(v).label }}</span>
          <span @click="toggle(v)"
            class="fonticon fonticon-cancel">
          </span>
        </span>
        <input
          ref="input"
          v-model="search"
          type="text"
          :placeholder="placeholder"
          class="autocomplete-input"
          :size="search.length || placeholder.length"
          @click="open = true;">
      </div>
      <div v-if="open"
        class="select-dropdown"
        :style="`height: ${38 * (options.length + 1)}px; max-height: ${38 * (internMaxVisible + 1)}px;`">
        <ul class="select-results">
          <template v-if="!grouped">
            <li v-for="option in innerOptions"
              :key="`${_uid}-${option.value}`"
              :class="{
              'result-option-disabled': option.disabled,
              'selected-item': value.includes(option.value)
            }"
              class="result-option"
              @click="!option.disabled ? toggle(option.value) : null">{{ option.label }}</li>
          </template>
          <template v-else>
            <li v-for="(options, groupName) in groupedOptions"
              :key="`${_uid}-${options.group}`"
              class="result-group">
              <span class="result-group-label">{{groupName}}</span>
              <ul class="result-group-sub">
                <li v-for="option in options"
                  :key="`${_uid}-${option.value}`"
                  :class="{
                  'result-option-disabled': option.disabled,
                  'selected-item': value.includes(option.value)
                }"
                  class="result-option"
                  @click="!option.disabled ? toggle(option.value) : null">{{ option.label }}</li>
              </ul>
            </li>
          </template>
        </ul>
      </div>
    </div>
    <span v-for="(error, pos) in errorBucket"
      :key="`${pos}-error-${error}`"
      style="display: block"
      class="form-control-error-text">
      {{ error }}
    </span>
    <span v-if="helper.length"
      class="form-control-helper-text">{{ helper }}</span>
  </div>
</template>
<script>
import inputable from '../mixins/inputable';
import validatable from '../mixins/validatable';
import disablable from '../mixins/disablable';

import {
  RegistrableInput,
} from '../mixins/registrable';
import clickoutside from '../directives/v-click-outside';

export default {
  name: 'vu-multiple-select',
  inheritAttrs: false,
  mixins: [inputable, disablable, validatable, RegistrableInput],
  directives: {
    'click-outside': clickoutside,
  },
  props: {
    grouped: {
      type: Boolean,
      default: () => false,
    },
    maxVisible: {
      type: Number,
      default: () => 3,
    },
    caseSensitive: {
      type: Boolean,
      default: false,
    },
  },
  data: () => ({
    open: false,
    search: '',
  }),
  computed: {
    innerOptions() {
      return this.caseSensitive ? this.options.filter((el) => (el.label.includes(this.search) || el.value.includes(this.search)))
        : this.options.filter((el) => (el.label.toLowerCase().includes(this.search.toLowerCase()) || el.value.toLowerCase().includes(this.search.toLowerCase())));
    },
    selected() {
      return this.options.find((el) => el.value === this.value) || {
        label: this.placeholder,
      };
    },
    groupedOptions() {
      return this.grouped ? this.options.reduce((acc, el) => {
        if (!acc[el.group]) acc[el.group] = [];
        acc[el.group].push(el);
        return acc;
      }, {}) : null;
    },
    internMaxVisible() {
      return this.maxVisible > this.options.length ? this.options.length : this.maxVisible;
    },
  },
  methods: {
    toggle(value) {
      this.search = '';

      if (this.value.includes(value)) {
        const values = this.value.slice();
        values.splice(values.indexOf(value), 1);
        this.$emit('input', values);
      } else {
        this.$emit('input', (this.value || []).concat([value]));
      }
    },
    getOption(value) {
      return this.options.find((el) => el.value === value) || {};
    },
  },
};
</script>
<style scoped lang="scss">

  .form-group {
    .autocomplete-searchbox {
      margin-bottom: 0;
    }

    &.has-error .autocomplete-searchbox {
      &, &:hover, &-active, &-active:hover
      {
        border-color: #EA4F37; //$red-1;
      }
    }
  }

  .autocomplete-searchbox {
      margin: 3px 0;

    .badge {
      margin-left: 3px;
    }

    .badge:first-of-type {
      margin-left: 0px;
    }

    &-active, &-active:hover {
      border-color: #368EC4;
    }
  }

  .select-dropdown {
    overflow-y: auto;
  }

  .selected-item {
    background: #F9F9F9;
    color: #3D3D3D;
    border-left: 5px solid #368EC4;
    padding-left: 9px;
  }

  .badge.badge-selectable:not(.badge-selected):not(.disabled):hover,
  .badge-selectable:focus {
    border-color: #77797C; // &grey-6;
    background-color: #E2E4E3; // &grey-3
  }

</style>
