<template>
  <span
    class="vu-dropdown-menu__wrap"
    v-click-outside="{
        handler: onClickOutside,
        events: ['click']
        }">

    <span
      v-if="activator"
      class="vu-dropdown-menu__activator"
      ref="activator"
      @click="() => onActivatorClick()"
    >
      <slot name="default"
       :active="isActive" />

      <span v-if="arrow && isActive"
      class="dropdown-root-arrow"
      :class="{ 'upwards': isDropup }" />
    </span>

    <span
      v-if="childrenActivator"
      ref="activator"
      @mouseenter="() => !isActive && !responsive && !isResponsive && enter()"
      @mouseleave="(e) => !responsive && !isResponsive && onMouseLeave(e)"
      >
        <slot name="children-activator" />
    </span>

    <div class="dropdown-menu dropdown-menu-root dropdown-root"
      v-show="isActive"
      :style="[{ 'zIndex': zIndex }]"
      :class="classes"
      ref="content">
      <ul
        class="dropdown-menu-wrap"
        :class="{ 'dropdown-menu-icons' : hasIcons }"
      >
        <li class="item item-back"
          v-if="responsive">
          <vu-icon icon="left-open" class="back-item" @click="$emit('back-click', parentItem)" />
          <span class="item-text">{{ parentItem.text }}</span>
        </li>

        <template
          v-for="item in items"
        >
          <vu-dropdownmenu
            v-if="item.items"
            :show="item.show"
            :key="item.name || item.text || item.label"
            :parentItem="item"
            :items="item.items"
            :attach="attach"
            :position="item.position"
            :disabled="item.disabled"
            :zIndex="zIndex + 1"
            :prerender="isPrerendered || prerender"
            :responsive="isResponsive || responsive"
            @back-click="onBackItemClick(item)"
            @update:show="(val) => { if(isResponsive || responsive) { item.show = val } }"
            v-on="$listeners"
          >
            <li
              slot="children-activator"
              class="item item-submenu"
              :class="{ 'selectable': item.selectable || item.selected, 'selected': item.selected, disabled: item.disabled }"
              @click="() => onItemClick(item)"
            >
              <vu-icon v-if="item.fonticon" :icon="item.fonticon" />
              <span class="item-text">
                {{ item.text || item.label || capitalize(item.name) }}
              </span>
              <div class="next-icon"
              >
                <span class="divider"></span>
                <vu-icon icon="right-open" />
              </div>
            </li>
          </vu-dropdownmenu>

          <li
             v-else-if="!item.class ||
              (!item.class.includes('header') && !item.class.includes('divider'))"
            :key="item.text || item.label"
            class="item"
            :class="{ 'selectable': item.selectable || item.selected, 'selected': item.selected, 'hidden': item.hidden, disabled: item.disabled }"
            @click="() => onItemClick(item)"
          >
            <span v-if="item.fonticon" class="fonticon" :class="`fonticon-${item.fonticon}`"></span>
            <span class="item-text">{{ item.text || item.label  || capitalize(item.name) }} {{ item.class }} </span>
          </li>

          <li
            v-else
            :key="item.text || item.label || uuid()"
            :class="item.class"
          >
            <span class="item-text">{{ item.text || item.label || capitalize(item.name) }}</span>
          </li>

        </template>
      </ul>

    </div>
  </span>
</template>

<script>

import uuid from '../utils/uuid';
import dasherize from '../utils/dasherize';
import capitalize from '../utils/capitalize';
import detachable from '../mixins/detachable';
import disablable from '../mixins/disablable';
import showable from '../mixins/showable';

function validate(items, isValid = true) {
  let valid = isValid;

  items.forEach((item) => {
    // Text must be defined if not divider
    if (!item.text && !item.label
      && (!item.class || !item.class.includes('divider'))) valid = false;
    if (item.items) valid = validate(item.items, valid);
  });
  return valid;
}

export default {
  name: 'vu-dropdownmenu',
  mixins: [detachable, disablable, showable],
  props: {
    value: {
      type: Boolean,
      default: () => false,
    },
    items: {
      type: Array,
      required: true,
      validator: validate,
    },
    show: {
      required: false,
    },
    preventDropup: {
      type: Boolean,
      default: () => false,
    },
    position: {
      type: String,
      required: false,
      default: 'bottom right',
    },
    arrow: {
      type: Boolean,
      default: () => false,
    },
    zIndex: {
      type: Number,
      default: () => 1000,
    },
    // INTERNAL PROPS
    parentItem: {
      type: Object,
      required: false,
    },
    prerender: {
      type: Boolean,
      default: () => false,
    },
    responsive: {
      type: Boolean,
      default: () => false,
    },
  },
  data: () => ({
    dropdown: true,
    uuid,
    dasherize,
    capitalize,
    isPrerendered: false,
    isResponsive: false,
    isDropup: false,
    openResponsiveMenu: null,
    width: 0,
    overflows: {},
    scrollableAncestors: [], // put in positionable
  }),
  computed: {
    classes() {
      return [
        { 'has-arrow': this.arrow },
        { dropup: this.isDropup },
        { 'responsive-menu': this.responsive || this.isResponsive },
        { 'dropdown-menu-responsive-wrap': this.responsive || this.isResponsive },
        { 'js-visible': this.prerender || this.isPrerendered },
      ];
    },
    activator() {
      return !!this.$scopedSlots.default;
    },
    isLeft() {
      return this.responsive ? this.$parent.isLeft : /left/.test(this.position);
    },
    isTop() {
      return this.responsive ? this.$parent.isTop : /top/.test(this.position);
    },
    childrenActivator() {
      return !!this.$slots['children-activator'];
    },
    isRoot() {
      return !this.$parent.dropdown;
    },
    hasIcons() {
      return this.items && this.items.some((item) => item.fonticon);
    },
  },
  watch: {
    value: {
      immediate: true,
      handler(val) {
        this.onShowUpdate(val);
      },
    },
    show: {
      immediate: true,
      handler(val) {
        this.onShowUpdate(val);
      },
    },
    position() {
      this.onShowUpdate(this.value || this.show);
    },
    attach() {
      this.onShowUpdate(this.value || this.show);
    },
  },
  destroyed() {
    this.stopScrollListening();
  },
  methods: {
    display() {
      this.resetPosition();
      this.isResponsive = false;
      this.isPrerendered = true;
      this.$nextTick(() => {
        this.setPosition({
          dropup: this.isTop,
          isLeft: this.isLeft,
        });
        if (this.isRoot) {
          this.getOverflows();
          const { responsive, shiftLeft, shiftRight } = this.overflows;
          if (responsive
            || (!this.isLeft && shiftRight)
            || (this.isLeft && shiftLeft)) {
            if (responsive) {
              this.isResponsive = responsive;
              this.resetPosition();
              this.setPosition({
                dropup: this.isTop,
                isLeft: this.isLeft,
              });
              this.getOverflows();
            }
            this.shiftPosition();
          }
        } else if (!this.responsive) {
          const { left, right } = this.getMenuOverflow();
          if (this.isLeft ? left : right) {
            this.resetPosition();
            this.setPosition({ isLeft: !this.isLeft });
          }
        }
        this.isActive = true;
        this.isPrerendered = false;
      });
    },
    enter() {
      if (!this.disabled) {
        this.scrollableAncestors = [];
        this.display();
        this.isActive = true;
        setTimeout(() => {
          if (this.isRoot
            && this.scrollableAncestors.length > 0) {
            this.scrollableAncestors.forEach((el) => el.addEventListener('scroll', () => this.leave()));
          }
        }, 50);
      }
    },
    leave() {
      this.isActive = false;
      if (this.items) {
        const items = this.openResponsiveMenu ? this.items.filter((a) => a.show && a !== this.openResponsiveMenu) : this.items;
        items.forEach((a) => {
          this.$set(a, 'show', false);
        });
      }
      this.stopScrollListening();
      if (!this.openResponsiveMenu) { this.resetPosition(); }
    },
    close() {
      this.leave();
      this.$emit('close');
    },
    getScrolls() {
      let topScroll = 0;
      let leftScroll = 0;
      let foundTarget = false;
      const { activator } = this.$refs;
      let parent = activator.parentElement;
      let wasScrollingElementConsidered;
      const { scrollingElement } = document;
      while (parent) {
        topScroll += parent.scrollTop;
        leftScroll += parent.scrollLeft;
        const { position, overflow } = window.getComputedStyle(parent);
        const props = overflow.split(' ');
        if (scrollingElement === parent) {
          wasScrollingElementConsidered = true;
        }
        if (!foundTarget
          && ['auto', 'scroll'].some((prop) => props.includes(prop))) {
          this.scrollableAncestors.push(parent);
        }
        if (parent === this.target) {
          foundTarget = true;
        }
        if (position === 'relative') {
          const rect = parent.getBoundingClientRect();
          const { top: topX, left: leftY } = rect;
          topScroll -= topX;
          leftScroll -= leftY;
          parent = false;
        } else {
          parent = parent.parentElement;
        }
      }
      if (!wasScrollingElementConsidered) {
        // Case the scrolling element is html element
        topScroll += scrollingElement.scrollTop;
        leftScroll += scrollingElement.scrollLeft;
      }
      return { topScroll, leftScroll };
    },
    resetPosition() {
      const { content: dropdown } = this.$refs;
      if (dropdown) {
        dropdown.style.top = '';
        dropdown.style.left = '';
        dropdown.style.right = '';
        dropdown.style.bottom = '';
      }
    },
    setPosition({
      dropUp, isLeft, leftOffset = 0,
    }) {
      const { content: dropdown, activator } = this.$refs;
      const rect = activator.getBoundingClientRect();
      const { top, left, right } = rect;
      const { topScroll, leftScroll } = this.getScrolls();
      if (this.responsive) {
        const { content: parentContent } = this.$parent.$refs;
        const { top: parentTop, left: parentLeft } = parentContent.style;
        if (dropUp) {
          dropdown.style.top = `${parseFloat(parentTop.replace('px', '')) + parentContent.offsetHeight - dropdown.offsetHeight}px`;
        } else {
          dropdown.style.top = parentTop;
        }
        if (isLeft) {
          dropdown.style.left = `${right - dropdown.offsetWidth}px`;
        } else {
          dropdown.style.left = parentLeft;
        }
      } else if (this.isRoot) {
        if (dropUp) {
          dropdown.style.top = `${top + topScroll - dropdown.getBoundingClientRect().height}px`;
        } else {
          dropdown.style.top = `${top + topScroll + rect.height}px`;
        }
        if (isLeft) {
          dropdown.style.left = `${right - dropdown.getBoundingClientRect().width + leftScroll + leftOffset}px`;
        } else {
          dropdown.style.left = `${left + leftScroll + leftOffset}px`;
        }
      } else if (isLeft) {
        const { right: targetRight } = this.target.getBoundingClientRect();
        dropdown.style.top = `${top + topScroll}px`;
        dropdown.style.right = `${targetRight - left + leftScroll}px`;
      } else {
        dropdown.style.top = `${top + topScroll}px`;
        dropdown.style.left = `${right + leftScroll}px`;
      }
      this.isDropup = dropUp;
    },
    getMenuOverflow() {
      const { left, right } = this.target.getBoundingClientRect();
      const childMenu = this.$refs.content;
      const rect = childMenu.getBoundingClientRect();
      return {
        left: rect.left < left,
        right: rect.right > right,
      };
    },
    getOverflows() {
      const width = this.isResponsive ? this.childrenMaxWidth() : this.childrenMaxWidthPair();
      const {
        left, right, top, bottom, width: targetWidth,
      } = this.target.getBoundingClientRect();
      const childMenu = this.$refs.content;
      const rect = childMenu.getBoundingClientRect();
      const Viewportheight = document.documentElement.clientHeight;
      this.overflows = {
        shiftLeft: (rect.right - width < left)
          && -(rect.right - width - left),
        shiftRight: (rect.left + width > right)
          && (rect.left + width - right),
        y: !this.preventDropup && (rect.top < top || top < 0 || rect.bottom > bottom || rect.bottom > Viewportheight),
        responsive: width > targetWidth,
        width,
      };
    },
    shiftPosition(side) {
      const {
        shiftLeft, shiftRight, verticalOverflow,
      } = this.overflows;
      this.resetPosition();
      // When the two largest menu pair cannot stand side by side
      // we decide to shift the top level menu closer to the closest
      // edge of the attach target.
      const isLeftCloser = this.isLeftCloser();
      let switchSide; let
        leftOffset;
      if (side === undefined
        && ((!isLeftCloser && !this.isLeft)
        || (isLeftCloser && this.isLeft))) {
        switchSide = true;
      } else if (side === false
        || (side === undefined && !this.isLeft)) {
        leftOffset = -shiftRight;
      } else if (side) {
        // menu is now going to the left
        // shift the amount of px required
        const childWidth = this.$refs.content.getBoundingClientRect().width;
        const activatorWidth = this.$refs.activator.getBoundingClientRect().width;
        leftOffset = shiftLeft - childWidth + activatorWidth;
      }
      this.setPosition({
        dropUp: verticalOverflow ? !this.isTop : this.isTop,
        isLeft: switchSide ? !this.isLeft : this.isLeft,
        leftOffset,
      });
      if (switchSide) {
        this.getOverflows();
        this.shiftPosition(!this.isLeft);
      }
    },
    icon(item) {
      return item.icon ? `fonticon-${item.icon}` : `fonticon-${item.fonticon}`;
    },
    isPartOfChild(el) {
      return this.$refs.content.contains(el);
    },
    childrenMaxWidth(depth = 0) {
      if (this.$children.some((a) => a.childrenMaxWidth)) {
        return this.$children
          .filter((a) => typeof a.childrenMaxWidth === 'function')
          .map((a) => a.childrenMaxWidth(depth + 1))
          .sort((a, b) => b.amount - a.amount)[0];
      }
      return this.$refs.content.offsetWidth;
    },
    childrenMaxWidthPair(depth = 0) {
      this.width = this.$refs.content.offsetWidth;
      if (this.$children.some((a) => a.childrenMaxWidthPair)) {
        return this.$children.filter((a) => typeof a.childrenMaxWidthPair === 'function')
          .map((child) => child.childrenMaxWidthPair(depth + 1))
          .sort((a, b) => b - a)[0];
      }
      // is undefined if not dropdownmenu
      const { width: parentWidth = 0 } = this.$parent;
      return parentWidth + this.width;
    },
    isLeftCloser() {
      const { left: targetLeft, right: targetRight } = this.target.getBoundingClientRect();
      const { left: activatorLeft, right: activatorRight } = this.$refs.activator.getBoundingClientRect();
      return activatorLeft - targetLeft < targetRight - activatorRight;
    },
    stopScrollListening() {
      if (this.isRoot && this.hasDetached && this.target) {
        this.scrollableAncestors.forEach((el) => el.removeEventListener('onscroll', this.leave));
      }
    },
    onShowUpdate(val) {
      this.isActive = val;
      if (val) {
        this.enter();
      } else {
        this.leave();
      }
    },
    onActivatorClick() {
      if (!this.isActive && !this.openResponsiveMenu) {
        this.enter();
      } else {
        this.openResponsiveMenu = null;
        this.close();
      }
    },
    onItemClick(item) {
      if (item.handler) {
        item.handler(item);
      }
      this.$emit(`click-${dasherize(item.name || item.text || item.label)}`);
      if (this.isResponsive || this.responsive) {
        this.openResponsiveMenu = item;
        this.$set(item, 'show', true);
        window.setTimeout(() => {
          this.leave();
        }, 50);
      } else {
        this.close();
      }
    },
    onBackItemClick(item) {
      this.$set(item, 'show', false);
      this.isActive = true;
    },
    onMouseLeave({ relatedTarget }) {
      if (!this.responsive && !this.isPartOfChild(relatedTarget)) {
        this.leave();
      }
    },
    // eslint-disable-next-line no-unused-vars
    onClickOutside({ target }) {
      if (target && !this.isPartOfChild(target)
        && !target.classList.contains('back-item')
        && (this.isActive
          || (this.isResponsive || this.responsive))) {
        this.openResponsiveMenu = null;
        this.close();
      }
    },
  },
};
</script>

<style lang="scss" scoped>
  .dropdown-menu.responsive-menu .item-submenu .next-icon {
    padding-right: 9px;
  }

  .js-visible {
    visibility: initial;
  }

  .vu-dropdown-menu__activator {
    display: inline-block;
    position: relative;
  }

  .dropdown-menu-icons > span > span > .item {
    padding-left: 0px;

    .item-text:nth-child(1) {
      padding-left: 14px;
    }
  }

  .dropdown-menu.dropdown-menu-root {
    margin: 0px;
  }

  .dropdown-menu.has-arrow:not(.dropup) {
    margin-top: 14px;
  }
  .dropdown-menu.has-arrow.dropup {
    margin-top: -14px;
  }

  .dropdown-root-arrow {
    position: absolute;
    top: calc(100%);
    left: calc(50% - 13px);
    display: block;
    font-size: 20px;

    &.upwards {
      top: unset;
      bottom: calc(100% + 14px);
    }

    &:before, &:after {
      content: "";
      border-style: solid;
      width: 0;
      height: 0;
      position: absolute;
    }

    &:before {
      border-width: 0 14px 15px 13px;
      border-color: rgba(154,154,154,0.44) transparent;
      transform: translateX(-1px) translateY(-1px);
    }

    &:after {
      border-color: #fff transparent;
      border-width: 0 12px 14px 12px;
    }
  }
</style>
