<template>
  <div v-if="isActive"
    class="datepicker datepicker-root"
    :style="styles"
    :class="className">
    <div class="datepicker-calendar">
      <div class="datepicker-title">
        <div class="datepicker-label">
          {{ currentMonth }}
          <select class="datepicker-select datepicker-select-month"
            v-model="month">
            <option v-for="option in selectableMonths"
              :key="option.value"
              :disabled="option.disabled"
              :value="option.value">
              {{ option.label}}
            </option>
          </select>
        </div>
        <div class="datepicker-label">
          {{ year }}
          <select class="datepicker-select datepicker-select-year"
            v-model="year">
            <option v-for="option in selectableYears"
              :key="option.value"
              :disabled="option.disabled"
              :value="option.value">
              {{ option.value }}
            </option>
         </select>
        </div>
        <button class="datepicker-prev" type="button"
        :class="{ 'is-disabled': !hasPrevMonth}"
        @click="hasPrevMonth && month--">
          {{ previousMonthLabel }}
        </button>
        <button class="datepicker-next" type="button"
        :class="{ 'is-disabled': !hasNextMonth}"
        @click="hasNextMonth && month++">
          {{ nextMonthLabel }}
        </button>
      </div>
      <vu-datepicker-table-date
        :date="date"
        :year="year"
        :month="month"
        :min="min"
        :max="max"
        :firstDay="firstDay"
        :unselectableDaysOfWeek="unselectableDaysOfWeek"
        :monthsLabels="monthsLabels"
        :weekdaysLabels="weekdaysLabels"
        :weekdaysShortLabels="weekdaysShortLabels"
        @select="onSelect($event)"
      />
    </div>
  </div>
</template>

<script>
import { parse } from '../utils/date';
import DatepickerTableDate from './vu-datepicker-table-date';
import showable from '../mixins/showable';
import rangeable from '../mixins/rangeable';

export default {
  name: 'vu-datepicker',
  mixins: [showable, rangeable],
  inheritAttrs: false,
  components: {
    'vu-datepicker-table-date': DatepickerTableDate,
  },
  props: {
    className: String,
    value: {
      type: [String, Date],
      default: () => '',
    },
    unselectableDaysOfWeek: {
      type: Array[Number],
      default: () => [],
    },
    yearRange: {
      type: Number,
      default: () => 10,
    },
    firstDay: {
      type: Number,
      default: () => 1,
    },
    // i18n
    previousMonthLabel: {
      type: String,
      default: () => 'Next Month',
    },
    nextMonthLabel: {
      type: String,
      default: () => 'Previous Month',
    },
    monthsLabels: {
      type: Array,
      default: () => ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
    },
    weekdaysLabels: {
      type: Array,
      default: () => ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'],
    },
    weekdaysShortLabels: {
      type: Array,
      default: () => ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'],
    },
  },
  data: () => ({
    left: 0,
    top: 38,
    month: 0,
    year: 0,
  }),
  computed: {
    styles() {
      return {
        top: `${this.top}px`,
        position: 'absolute',
      };
    },
    date: {
      get() {
        return this.value;
      },
      set(value) {
        return this.$emit('select', value);
      },
    },
    isEmpty() {
      return this.value === null || this.value === '' || this.value === undefined;
    },
    currentMonth() {
      return this.monthsLabels[this.month];
    },
    minYear() {
      return new Date(this.min).getFullYear();
    },
    minMonth() {
      return new Date(this.min).getMonth();
    },
    maxYear() {
      return new Date(this.max).getFullYear();
    },
    maxMonth() {
      return new Date(this.max).getMonth();
    },
    hasPrevMonth() {
      return !(this.year === this.minYear && (this.month === 0 || this.minMonth >= this.month));
    },
    hasNextMonth() {
      return !(this.year === this.maxYear && (this.month === 11 || this.maxMonth <= this.month));
    },
    selectableMonths() {
      return this.monthsLabels.map((label, index) => {
        const disabled = (this.year === this.minYear && index < this.minMonth) || (this.year === this.maxYear && index > this.maxMonth);
        return {
          value: index, label, disabled,
        };
      });
    },
    selectableYears() {
      const min = Math.max(this.year - this.yearRange, this.minYear);
      const max = Math.min(this.year + 1 + this.yearRange, this.maxYear + 1);
      const arr = Array(max - min).fill({});
      return arr.map((val, index) => ({ value: min + index }));
    },
  },
  watch: {
    isActive(val) {
      if (val) {
        this.setCurrent();
      }
    },
    value() { if (this.isActive) this.setCurrent(); },
    month(month) {
      if (month > 11) {
        this.year++;
        this.month = 0;
      } else if (month < 0) {
        this.month = 11;
        this.year--;
      }
    },
  },
  methods: {
    setCurrent() {
      const date = parse(this.date) || new Date();
      this.month = date.getMonth();
      this.year = date.getFullYear();
    },
    onSelect(value) {
      this.month = value.getMonth();
      this.year = value.getFullYear();
      this.date = value;
    },
  },
};
</script>

