import rangeable from '../mixins/rangeable';
import { isValidDate, getDaysInMonth, compareDates } from '../utils/date';

export default {
  name: 'vu-datepicker-table-date',
  mixins: [rangeable],
  props: {
    date: {
      type: Date,
    },
    year: {
      type: Number,
      required: true,
    },
    month: {
      type: Number,
      required: true,
    },
    unselectableDaysOfWeek: {
      type: Array[Number],
      default: () => [],
    },
    firstDay: {
      type: Number,
      default: () => 0,
    },
    // i18n
    weekdaysLabels: {
      type: Array,
      required: true,
    },
    weekdaysShortLabels: {
      type: Array,
      required: true,
    },
  },
  methods: {
    renderTable(createElement, rows) {
      return createElement('table', {
        class: 'datepicker-table',
        attrs: { cellspacing: '0', cellpadding: '0' },
      }, [
        this.renderHead(createElement),
        this.renderBody(createElement, rows),
      ]);
    },
    renderHead(createElement) {
      const arr = [];
      for (let i = 0; i < 7; i++) {
        const element = createElement('th', {
          attrs: { scope: 'col', cellspacing: '0', cellpadding: '0' },
        }, [
          createElement('abbr', {
            attrs: {
              title: this.renderDayName(i),
            },
          }, this.renderDayName(i, true)),
        ]);
        arr.push(element);
      }
      return createElement('thead', {}, arr);
    },
    renderBody(createElement, rows) {
      return createElement('tbody', {}, rows);
    },
    renderWeek(createElement, d, m, y) {
      // Lifted from http://javascript.about.com/library/blweekyear.htm, lightly modified.
      const oneJan = new Date(y, 0, 1);
      const weekNum = Math.ceil((((new Date(y, m, d) - oneJan) / 86400000) + oneJan.getDay() + 1) / 7);
      const className = `datepicker${this.week}`;
      return createElement('td', { class: className }, weekNum);
    },
    renderDayName(day, abbr) {
      let d = day + this.firstDay;
      while (d >= 7) {
        d -= 7;
      }
      return abbr ? this.weekdaysShortLabels[d] : this.weekdaysLabels[d];
    },
    renderDay(createElement, d, m, y, isSelected, isToday, isDisabled, isEmpty) {
      const arr = [];
      if (isEmpty) {
        return createElement('td', { class: 'is-empty' });
      }
      if (isDisabled) {
        arr.push('is-disabled');
      }
      if (isToday) {
        arr.push('is-today');
      }
      if (isSelected) {
        arr.push('is-selected');
      }
      return createElement('td', {
        class: arr.join(' '),
        attrs: {
          'data-day': d,
        },
      }, [
        createElement('button', {
          class: 'datepicker-button datepicker-name',
          attrs: {
            type: 'button',
            'data-year': y,
            'data-month': m,
            'data-day': d,
          },
          on: {
            click: this.onSelect,
          },
        }, d),
      ]);
    },
    renderRow(createElement, days) {
      return createElement('tr', {}, days);
    },
    onSelect(event) {
      const year = event.target.getAttribute('data-year');
      const month = event.target.getAttribute('data-month');
      const day = event.target.getAttribute('data-day');

      this.$emit('select', new Date(year, month, day));
    },
  },
  render(createElement) {
    const now = new Date();
    now.setHours(0, 0, 0, 0);

    const days = getDaysInMonth(this.year, this.month);
    let before = new Date(this.year, this.month, 1).getDay();
    const elements = [];
    let row = [];
    let cells;
    let after;

    if (this.firstDay > 0) {
      before -= this.firstDay;
      if (before < 0) {
        before += 7;
      }
    }

    cells = days + before;
    after = cells;

    while (after > 7) {
      after -= 7;
    }
    cells += 7 - after;

    for (let i = 0, r = 0; i < cells; i++) {
      const day = new Date(this.year, this.month, 1 + (i - before));

      /* eslint-disable valid-typeof */
      const min = Date.parse(this.min);
      const max = Date.parse(this.max);

      const isDisabled = (min && day < min) || (max && day > max) || (this.unselectableDaysOfWeek && this.unselectableDaysOfWeek.indexOf(day.getDay()) > -1);
      const isSelected = isValidDate(this.date) ? compareDates(day, this.date) : false;
      const isToday = compareDates(day, now);
      const isEmpty = i < before || i >= (days + before);

      row.push(this.renderDay(createElement, 1 + (i - before), this.month, this.year, isSelected, isToday, isDisabled, isEmpty));

      if (++r === 7) {
        if (this.showWeekNumber) {
          row.unshift(this.renderWeek(createElement, i - before, this.month, this.year));
        }
        elements.push(this.renderRow(createElement, row, this.isRTL));
        row = [];
        r = 0;
      }
    }
    return this.renderTable(createElement, elements);
  },
};
