/* eslint-disable no-param-reassign */
import message from './components/vu-message';
import modal from './components/vu-modal';
import lightbox from './components/vu-lightbox';

import directive from './directives/index';

import './styles/index.scss';

// Auto imports all vue components
const req = require.context('./components', true, /.vue$/);
const components = req.keys().map(req);
// module.exports = req;

const install = (Vue, config) => {
  components.forEach((el) => {
    Vue.component(el.default.name, el.default);
  });

  message.install(Vue);
  modal.install(Vue);
  lightbox.install(Vue);
  directive.install(Vue, config);

  Vue.prototype.$message = message.message;
  Vue.prototype.$lightbox = lightbox.lightbox;
  Vue.prototype.$alert = modal.alert;
  Vue.prototype.$confirm = modal.confirm;
  Vue.prototype.$prompt = modal.prompt;
  Vue.prototype.$dialog = modal.dialog;
};

if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue);
}

export default install;
export { install };
