<template>
  <div class="form-group"
    :classes="classes">
    <label class="control-label"
      v-if="label.length">{{ label }}<span v-if="required" class="label-field-required"> *</span></label>
    <div v-click-outside="function () {open = false}"
      ref="activator"
      class="input-date">
      <input
        ref="input"
        v-bind="$attrs"
        :value="stringifedValue"
        :placeholder="placeholder"
        :disabled="disabled"
        readonly
        type="text"
        class="form-control input-date"
        @click="open = true;"
        :class="{'filled': !isEmpty}"
      />
      <span
        v-if="clearable"
        class="input-date-reset fonticon fonticon-clear"
        @click="click()"
      />
      <vu-datepicker
        :value="value"
        :show="open"
        :className="pickerClass"
        :min="min"
        :max="max"
        :unselectableDaysOfWeek="unselectableDaysOfWeek"
        :yearRange="yearRange"
        :firstDay="firstDay"
        :previousMonthLabel="previousMonthLabel"
        :nextMonthLabel="nextMonthLabel"
        :monthsLabels="monthsLabels"
        :weekdaysLabels="weekdaysLabels"
        :weekdaysShortLabels="weekdaysShortLabels"
        @select="handleSelect"
        @boundary-change="date = $event.value"
      />
    </div>
    <span
      v-for="(error, pos) in errorBucket"
      :key="`${pos}-error-${error}`"
      style="display: block"
      class="form-control-error-text">
      {{ error }}
    </span>
    <span
      v-if="helper.length"
      class="form-control-helper-text">{{ helper }}</span>
  </div>
</template>

<script>

import Inputable from '../mixins/inputable';
import Rangeable from '../mixins/rangeable';
import Clearable from '../mixins/clearable';
import Validatable from '../mixins/validatable';
import Disablable from '../mixins/disablable';

import { RegistrableInput } from '../mixins/registrable';

import clickoutside from '../directives/v-click-outside';

export default {
  name: 'vu-input-date',
  directives: {
    'click-outside': clickoutside,
  },
  mixins: [Inputable, Rangeable, Clearable, Validatable, RegistrableInput, Disablable],
  inheritAttrs: false,
  props: {
    value: {
      type: Date,
      default: () => new Date(),
    },
    pickerClass: {
      type: String,
      default: () => '',
    },
    unselectableDaysOfWeek: {
      type: Array[Number],
      default: () => [],
    },
    yearRange: {
      type: Number,
      default: () => 10,
    },
    firstDay: {
      type: Number,
      default: () => 1,
    },
    // input
    placeholder: {
      type: String,
      default: () => 'Select a value',
    },
    // i18n
    dateFormatLocale: {
      type: String,
      default: () => 'en',
    },
    dateFormatOptions: {
      type: Object,
      default: () => ({
        weekday: 'short',
        year: 'numeric',
        month: 'short',
        day: '2-digit',
      }),
    },
    hideOnSelect: {
      type: Boolean,
      default: () => true,
    },
    previousMonthLabel: {
      type: String,
    },
    nextMonthLabel: {
      type: String,
    },
    monthsLabels: {
      type: Array,
    },
    weekdaysLabels: {
      type: Array,
    },
    weekdaysShortLabels: {
      type: Array,
    },
  },
  data: () => ({
    open: false,
    stringifedValue: '',
  }),
  computed: {
    date: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit('input', value);
      },
    },
    isEmpty() {
      return this.value === null || this.value === '' || this.value === undefined;
    },
  },
  watch: {
    date: {
      immediate: true,
      handler() {
        if (this.date) {
          this.stringifedValue = new Intl.DateTimeFormat(this.dateFormatLocale, this.dateFormatOptions).format(this.date);
        } else {
          this.stringifedValue = '';
        }
      },
    },
  },
  methods: {
    click() {
      this.date = '';
    },
    handleSelect(event) {
      this.date = event;
      if (this.hideOnSelect) {
        this.open = false;
      }
    },
  },
};
</script>

<style scoped>

.input-date:not(.disabled) input.form-control {
    padding-right: 27px;
}

.input-date .fonticon-calendar {
    position: absolute;
    left: 6px;
    top: 10px;
}

</style>
