<template>
  <div ref="content" v-on="$listeners" :class="[`fade ${side} ${type} ${type}-root`, { in: open, 'tooltip--inner': !attach}]">
    <div :class="`${type}-arrow`"></div>
    <div ref="body" :class="`${type}-body`">
      <span v-if="text" v-html="text"></span>
      <slot v-else />
    </div>
  </div>
</template>
<script>

export default {
  name: 'vu-tooltip',
  props: {
    type: {
      type: String,
      default: () => 'tooltip',
    },
    side: {
      type: String,
      default: () => 'top',
    },
    text: {
      type: String,
      default: () => '',
    },
    attach: {
      default: () => false,
    },
    removable: {
      default: () => false,
    },
    open: {
      type: Boolean,
      default: () => false,
    },
  },
};
</script>
<style scoped lang="scss">
  .popover {
    color: #3D3D3D;
  }

  .tooltip, .popover {
    max-width: 500px;
    width: max-content;
  }

  .tooltip-body, .popover-body {
    width: 100%;
    max-width: 500px;
  }

  .tooltip--inner {
    position: absolute;

    &.tooltip.bottom, &.popover.bottom {
      bottom: 0;
      left: 50%;
      transform: translateX(-50%) translateY(100%) translateY(8px); /* IE doesn't support translate + calc. We need to use multiple translate */
    }

    &.tooltip.top, &.popover.top {
      top: 0;
      left: 50%;
      transform: translate(-50%, -100%);
    }

    &.tooltip.left, &.popover.left {
      top: 50%;
      left: 0;
      transform: translate(-100%, -50%);
    }

    &.tooltip.right, &.popover.right {
      top: 50%;
      right: 0;
      transform: translateX(100%) translateX(8px) translateY(-50%); /* IE doesn't support translate + calc. We need to use multiple translate */
    }
  }

  .fonticon .tooltip {
    font-family: Arial, sans-serif;
  }
</style>
