      :icon="icon"
      :active="active">
    </vu-icon>
    <template v-if="$slots.default">
      <span class="icon-link__link">
        <slot name="default" />
      </span>
    </template>
    <template v-else><span class="icon-link__link">{{ label }}</span></template>
  </a>
</template>
<script>
import activable from '../mixins/activable';

export default {
  name: 'vu-icon-link',
  mixins: [activable],
  props: {
    label: {
      type: String,
      default: () => '',
    },
    icon: {
      type: String,
      default: () => '',
    },
  },
  data: () => ({
    pressed: false,
  }),
};
</script>

