import Modal from './vu-modal.vue';

let modalInstance;
let ModalConstructor;

const noop = () => {};

const defaults = {
  title: '',
  message: '',
  rawContent: '',
  show: false,
  keepRendered: false,
  showCancelIcon: true,
  showCancelButton: false,
  showInput: false,
  showFooter: true,
  label: '',
  required: false,
  helper: '',
  placeholder: '',
  color: '',
  rules: [],
  cancelLabel: 'Cancel',
  okLabel: 'OK',
  onClose: noop,
  onConfirm: noop,
};

const install = (Vue) => {
  ModalConstructor = Vue.extend(Modal);
};

const setup = () => {
  if (modalInstance) return;
  modalInstance = new ModalConstructor();
  modalInstance.$mount();
  document.body.insertBefore(modalInstance.$el, document.body.childNodes[0]);
};

const open = (options) => {
  setup();

  const values = { ...defaults, ...(options || {}) };
  const props = modalInstance.$props;

  Object.keys(props).forEach((key) => {
    props[key] = values[key];
  });

  props.show = true;

  return new Promise((resolve, reject) => {
    modalInstance.$once('confirm', (event) => {
      props.show = false;
      modalInstance.$off('close');
      modalInstance.$off('cancel');
      resolve({ event: 'confirm', value: event });
    });
    modalInstance.$once('close', () => {
      props.show = false;
      modalInstance.$off('confirm');
      modalInstance.$off('cancel');
      // eslint-disable-next-line prefer-promise-reject-errors
      reject({ event: 'close', value: false });
    });
    modalInstance.$once('cancel', () => {
      props.show = false;
      modalInstance.$off('close');
      modalInstance.$off('confirm');
      // eslint-disable-next-line prefer-promise-reject-errors
      reject({ event: 'cancel', value: false });
    });
  });
};

const alert = (message, title, options) => {
  setup();

  if (typeof title === 'object') {
    // eslint-disable-next-line no-param-reassign
    options = title;
    // eslint-disable-next-line no-param-reassign
    title = '';
  } else if (title === undefined) {
    // eslint-disable-next-line no-param-reassign
    title = '';
  }

  if (typeof message === 'object') {
    // eslint-disable-next-line no-param-reassign
    options = { ...options, ...message };
    // eslint-disable-next-line no-param-reassign
    message = '';
  }

  return open({
    title,
    message,
    showCancelButton: false,
    showCancelIcon: false,
    ...(options || {}),
  });
};

const confirm = (message, title, options) => {
  setup();

  if (typeof title === 'object') {
    // eslint-disable-next-line no-param-reassign
    options = title;
    // eslint-disable-next-line no-param-reassign
    title = '';
  } else if (title === undefined) {
    // eslint-disable-next-line no-param-reassign
    title = '';
  }

  if (typeof message === 'object') {
    // eslint-disable-next-line no-param-reassign
    options = { ...options, ...message };
    // eslint-disable-next-line no-param-reassign
    message = '';
  }

  return open({
    title,
    message,
    showCancelButton: true,
    showCancelIcon: true,
    ...(options || {}),
  });
};

const prompt = (message, title, options) => {
  setup();

  if (typeof title === 'object') {
    // eslint-disable-next-line no-param-reassign
    options = title;
    // eslint-disable-next-line no-param-reassign
    title = '';
  } else if (title === undefined) {
    // eslint-disable-next-line no-param-reassign
    title = '';
  }

  if (typeof message === 'object') {
    // eslint-disable-next-line no-param-reassign
    options = { ...options, ...message };
    // eslint-disable-next-line no-param-reassign
    message = '';
  }

  return open({
    title,
    message,
    showCancelButton: true,
    showCancelIcon: true,
    showInput: true,
    ...(options || {}),
  });
};

export default {
  install, open, alert, confirm, prompt,
};
