// Following Vuetify mixin pattern
const validateAttachTarget = (val) => {
  const type = typeof val;

  if (type === 'boolean' || type === 'string') return true;

  return val.nodeType === Node.ELEMENT_NODE;
};

export default {
  name: 'detachable',
  props: {
    attach: {
      default: () => false,
      validator: validateAttachTarget,
    },
    contentClass: {
      type: String,
      default: '',
    },
  },
  data: () => ({
    hasDetached: false,
    target: null,
  }),
  watch: {
    attach() {
      this.hasDetached = false;
      this.initDetach();
    },
  },
  mounted() {
    this.initDetach();
  },
  beforeDestroy() {
    if (this.hasDetached) {
      if (this.contentClass) {
        document.querySelector(`.${this.contentClass}`).remove();
      } else {
        this.$refs.content.remove();
      }
    }
  },
  methods: {
    initDetach() {
      if (this._isDestroyed
        || this.hasDetached
        // Leave menu in place if attached
        // and dev has not changed target
        || this.attach === '' // If used as a boolean prop (<v-menu attach>)
        || this.attach === true // If bound to a boolean (<v-menu :attach="true">)
        || this.attach === 'attach' // If bound as boolean prop in pug (v-menu(attach))
      ) return;

      let target;
      if (this.attach === false) {
        // Default, detach to body
        target = document.body;
      } else if (typeof this.attach === 'string') {
        // CSS selector
        target = document.querySelector(this.attach);
      } else {
        // DOM Element
        target = this.attach;
      }

      if (!target) {
        // eslint-disable-next-line no-console
        console.warn(`Unable to locate target ${this.attach}`, this);
        return;
      }

      // eslint-disable-next-line no-mixed-operators
      target.appendChild(this.contentClass && document.querySelector(`.${this.contentClass}`) || this.$refs.content);
      this.target = target;
      this.hasDetached = true;
    },
  },

};
