<template>
  <div class="form-group"
    :class="classes">
    <label class="control-label"
      v-if="label.length">{{ label }}<span v-if="required" class="label-field-required"> *</span></label>
    <div v-click-outside="function () { open = false; search = (value && selected.label) || value}"
      @click="open = !open && !disabled; search = (value && selected.label) || value"
      :class="['select', {
        'select-placeholder': !autocomplete,
        'select-not-chosen': !autocomplete && !value,
        'dropdown-visible': open,
        'select-disabled': disabled,
        'select-autocomplete': autocomplete,
        'select-clearable': clearable,
        'select-focus': focused
      }]">
      <input v-if="autocomplete" :disabled="disabled" :placeholder="selected.label" class="form-control" v-model="search"/>
      <vu-icon-btn v-if="value && (autocomplete || clearable)"
        icon="clear"
        class="select__clear-icon"
        :class="{'select--has-handle': autocomplete }"
        @click="$emit('input', ''); search = ''">
      </vu-icon-btn>
      <select v-if="!autocomplete && value" class="form-control select-hidden"
        :disabled="disabled"
        @focus="focused = true"
        @blur="blur()"
        @keydown="(event) => innerSelectKeydown(event)"/>
      <div v-if="!autocomplete" class="select-handle" />
      <ul v-if="!autocomplete" class="select-choices form-control">
        <li class="select-choice">{{ selected.label }}</li>
      </ul>
      <div v-if="open"
        class="select-dropdown"
        :style="`height: ${38 * (innerOptions.length + (!autocomplete && !hidePlaceholderOption ? 1 : 0))}px; max-height: ${38 * (internMaxVisible + 1)}px;`">
        <ul class="select-results">
          <li v-if="!autocomplete && !hidePlaceholderOption" class="result-option result-option-placeholder"
            @click="$emit('input', ''); search = ''">{{ placeholder }}</li>
          <template v-if="!grouped">
            <li v-for="option in innerOptions"
              :key="`${_uid}-${ option.value || option.label }`"
              :class="{
                'result-option-disabled': option.disabled,
                'result-option-selected': (option.value === value)
              }"
              class="result-option"
              @click="option.disabled ? null : $emit('input', option.value); search = option.label">{{ option.label }}</li>
          </template>
          <template v-else>
            <li v-for="(options, groupName) in groupedOptions"
              :key="`${_uid}-${options.group}`"
              class="result-group">
              <span class="result-group-label">{{groupName}}</span>
              <ul class="result-group-sub">
                <li v-for="option in options"
                  :key="`${_uid}-${option.value}`"
                  :class="{
                    'result-option-disabled': option.disabled,
                    'result-option-selected': (option.value === value)
                  }"
                  class="result-option"
                  @click="option.disabled ? null : $emit('input', option.value)">{{ option.label }}</li>
              </ul>
            </li>
          </template>
        </ul>
      </div>
    </div>
    <span v-for="(error, pos) in errorBucket"
      :key="`${pos}-error-${error}`"
      style="display: block"
      class="form-control-error-text">
      {{ error }}
    </span>
    <span v-if="helper.length"
      class="form-control-helper-text">{{ helper }}</span>
  </div>
</template>
<script>
import inputable from '../mixins/inputable';
import clearable from '../mixins/clearable';
import validatable from '../mixins/validatable';
import disablable from '../mixins/disablable';
import { RegistrableInput } from '../mixins/registrable';
import clickoutside from '../directives/v-click-outside';

export default {
  name: 'vu-select',
  inheritAttrs: false,
  mixins: [inputable, clearable, disablable, validatable, RegistrableInput],
  directives: {
    'click-outside': clickoutside,
  },
  props: {
    autocomplete: {
      type: Boolean,
      default: () => false,
    },
    hidePlaceholderOption: {
      type: Boolean,
      default: () => false,
    },
    grouped: {
      type: Boolean,
      default: () => false,
    },
    maxVisible: {
      type: Number,
      default: () => 5,
    },
  },
  data: () => ({
    open: false,
    focused: false,
    search: '',
  }),
  watch: {
    value() {
      this.search = this.selected.label;
    },
  },
  created() { this.search = (this.value && this.selected.label) || this.value; },
  computed: {
    innerOptions() {
      return this.autocomplete
        ? this.options
          .filter((el) => (el.label.toLowerCase().includes(this.search.toLowerCase()) || el.value.toLowerCase().includes(this.search.toLowerCase())))
        : this.options;
    },
    selected() {
      return this.options.find((el) => el.value === this.value) || {
        label: this.placeholder,
      };
    },
    groupedOptions() {
      return this.grouped ? this.options.reduce((acc, el) => {
        if (!acc[el.group]) acc[el.group] = [];
        acc[el.group].push(el);
        return acc;
      }, {}) : null;
    },
    internMaxVisible() {
      return this.maxVisible > this.options.length ? this.options.length : this.maxVisible;
    },
  },
  methods: {
    innerSelectKeydown(e) {
      switch (e.code) {
        case 'Space':
        case 'Enter':
        case 'NumpadEnter':
          this.open = !this.open;
          e.preventDefault();
          e.stopPropagation();
          break;
        case 'Escape':
          this.open = false;
          e.preventDefault();
          e.stopPropagation();
          break;
        case 'up':
          break;
        case 'down':
          break;
        default:
          break;
      }
    },
    blur() {
      this.focused = false; this.open = false;
    },
  },
};
</script>
<style scoped lang="scss">

  .form-group .form-control {
    margin-bottom: 0px;
  }

  .select-dropdown {
    overflow-y: auto
  }

  .select.select-autocomplete:before {
    display: none;
  }

  .select.select-autocomplete:after {
    display: none;
  }

  .select__clear-icon {
    position: absolute;
    color: #77797C;
    font-size: 18px;
    bottom: 0;
    right: 0;

     &:not(.select--has-handle) {
      right: 38px;
    }
  }
</style>
