<template>
  <div ref="scroll-container"
    :class="[{ 'vu-scroll-container--reverse': reverse, 'vu-scroll-container--horizontal': horizontal }, 'vu-scroll-container']">
    <div class="vu-scroll-container__inner">
      <slot />
      <vu-lazy v-if="infinite"
        :key="`lazy-key-${lazyKeyIndex}`"
        @intersect="$emit('loading')"
        :options="{ root: $refs['scroll-container'], rootMargin }"
        height="30px"
        style="min-width: 30px">
        <vu-spinner :text="loadingText" />
      </vu-lazy>
    </div>
  </div>
</template>
<script>
export default {
  name: 'vu-scroller',
  props: {
    reverse: {
      type: Boolean,
      default: false,
    },
    infinite: {
      type: Boolean,
      default: false,
    },
    infiniteMargin: {
      type: [String, Number],
      default: 200,
    },
    loadingText: {
      type: String,
      default: '',
    },
    horizontal: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      lazyKeyIndex: 0,
    };
  },
  computed: {
    rootMargin() {
      return Array(4).fill(`${this.infiniteMargin}px`).join(' ');
    },
  },
  methods: {
    stopLoading() {
      this.lazyKeyIndex += 1;
    },
  },
};

</script>
<style lang="scss" scoped>
  @import '../styles/_variables.scss';

  $scroll-bar-width: 6px;
  $scroll-bar-color: $grey-5;
  $scroll-bar-color_hover: $grey-6;
  $delay-to-hide: 0.7s;
  $transition-duration: 0.2s;
  $scroll-bar-bacground-color: transparent;

  .vu-scroll-container {
    $selector: &;

    /** CSS for support of horizontal and reverse scroll */

    height: 100%;
    width: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;

    #{$selector}__inner {
      background-color: white;
      display: flex;
      flex-direction: column;
    }

    &--reverse {
      flex-direction: column-reverse;

      #{$selector}__inner {
        flex-direction: column-reverse;
      }
    }

    &--horizontal {
      overflow-y: hidden;
      overflow-x: scroll;
      flex-direction: row;

      #{$selector}__inner {
        flex-direction: row;
      }

      &#{$selector}--reverse {
        flex-direction: row-reverse;

        #{$selector}__inner {
          flex-direction: row-reverse;
        }
      }
    }

    /** CSS for styling scroll bar */

    /* trick to only apply in webkit */
    background-color: $scroll-bar-bacground-color;
    background-clip: text;
    /* uncomment following line to see the trick in actions */
    transition-duration: $transition-duration;
    transition-property: background-color scrollbar-color;
    transition-delay: $delay-to-hide;
    scrollbar-color: white white;
    scrollbar-width: thin;

    &:hover {
      scrollbar-color: $scroll-bar-color white;
      background-color: $scroll-bar-color;
      transition-delay: 0s;
    }

    &::-webkit-scrollbar {
      width: $scroll-bar-width;
      height: $scroll-bar-width;
    }

    &::-webkit-scrollbar-track {
      display: none;
    }

    &::-webkit-scrollbar-thumb {
      border-radius: 10px;
      background-color: inherit;
    }

    &::-webkit-scrollbar-thumb:hover {
      background-color: $scroll-bar-color_hover;
    }
  }

</style>
