<template>
  <div
    :class="['vu-grid-view', { elevated, 'vu-grid-view--rich': rich }, classes]"
    @wheel="scrollHorizontal"
    v-mask="loading"
  >
    <div
      class="grid-view__container"
      :style="`height: ${
        (dense ? 24 : 38) +
        (dense ? 24 : 38) *
          (sortedItems.length < rowsPerPage ? sortedItems.length : rowsPerPage)
      }px;`"
    >
      <table
        :class="[
          'grid-view__table',
          { dense: dense, 'grid-view__table--has-selection': hasSelected },
        ]"
      >
        <thead>
          <tr>
            <th v-if="selectable" class="grid-view__table__header-intersection">
              <vu-checkbox
                v-if="allSelectable"
                dense
                class="grid-view__table__checkbox"
                :value="value.length === items.length && items.length"
                :options="[{}]"
                @input="selectAll"
              />
            </th>
            <th
              v-for="(header, index) in headers"
              :key="`header_${header.property}_${index}`"
            >
              {{ header.label }}
              <vu-icon-btn
                class="icon-smaller"
                v-if="header.sortable !== false"
                :icon="
                  header.property === sortKey && isAscending ? 'expand-up' : 'expand-down'
                "
                :active="header.property === sortKey"
                @click="sortBy(header.property)"
              />
            </th>
          </tr>
        </thead>
        <tbody class="grid-view__table__body">
          <tr
            :class="{ dense, selected: value.includes(item) }"
            v-for="(item, index) in sortedItems"
            :key="`line_${index}`"
            @click="selectItem(item)"
          >
            <td class="grid-view__table__row__header" v-if="selectable">
              <vu-checkbox
                dense
                class="grid-view__table__body__checkbox"
                @input="selectItem(item)"
                :value="value.includes(item)"
                :options="[{}]"
              />
            </td>
            <td
              v-for="header in headers"
              :key="`${header.property}_${item[header.property]}`"
              :class="[
                isEqual(item, selectedCellItem) &&
                isEqual(header.property, selectedCellProperty)
                  ? 'selected'
                  : '',
              ]"
              @click="
                () => {
                  selectedCellItem = item;
                  selectedCellProperty = header.property;
                  $emit('cellClick', { item, header, property });
                }
              "
            >
              <slot :name="header.property" v-bind="item">
                {{ item[header.property] }}
              </slot>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div
      class="grid-view__pagination"
      :class="{ 'grid-view__pagination--top': topPagination }"
    >
      <slot name="pagination">
        <vu-select
          :options="itemPerPageOptions.map((el) => ({ value: el, label: el }))"
          :rules="[(v) => v.length > 0]"
          :hidePlaceholderOption="true"
          :value="rowsPerPage"
          @input="updateRows"
        />
        <div style="margin-right: 5px">
          {{ startRow + 1 }}-{{ itemMax }} / {{ serverItemsLength || items.length }}
        </div>
        <vu-btn :disabled="startRow === 0" @click="pageDown">
          {{ labels.previousLabel }}
        </vu-btn>
        <vu-btn
          :disabled="startRow + rowsPerPage >= (serverItemsLength || items.length)"
          @click="pageUp"
        >
          {{ labels.nextLabel }}
        </vu-btn>
      </slot>
    </div>
  </div>
</template>
<script>
import loadable from '../mixins/loadable';
import elevable from '../mixins/elevable';
import VuBtn from './vu-btn.vue';

export default {
  name: 'vu-grid-view',
  components: {
    VuBtn,
  },
  mixins: [loadable, elevable],
  props: {
    value: {
      type: [Object, Array],
      default: () => [],
    },
    items: {
      type: Array,
      required: true,
    },
    headers: {
      type: Array,
      required: true,
    },
    dense: {
      type: Boolean,
      default: false,
    },
    rich: {
      type: Boolean,
      default: true,
    },
    selectable: {
      type: Boolean,
      default: false,
    },
    allSelectable: {
      type: Boolean,
      default: true,
    },
    serverItemsLength: {
      type: Number,
    },
    rowsPerPage: {
      type: Number,
      default: 5,
    },
    topPagination: {
      type: Boolean,
      default: false,
    },
    whiteBackground: {
      type: Boolean,
      default: false,
    },
    sort: {
      type: Function,
      default(a, b) {
        if (this.isAscending) {
          if (a[this.sortKey] < b[this.sortKey]) {
            return -1;
          }
          if (a[this.sortKey] > b[this.sortKey]) {
            return 1;
          }
          return 0;
        }
        if (a[this.sortKey] > b[this.sortKey]) {
          return -1;
        }
        if (a[this.sortKey] < b[this.sortKey]) {
          return 1;
        }
        return 0;
      },
    },
    itemPerPageOptions: {
      type: Array,
      default: () => [10, 20, 50],
    },
    labels: {
      type: Object,
      default: () => ({
        previousLabel: 'Previous',
        nextLabel: 'Next',
      }),
    },
  },
  data() {
    return {
      sortKey: '',
      isAscending: undefined,
      startRow: 0,
      selectedCellItem: '',
      selectedCellProperty: '',
    };
  },
  computed: {
    hasSelected() {
      return this.value.length > 0;
    },
    sortedItems() {
      const endRow = this.startRow + this.rowsPerPage;
      if (!this.sortKey) {
        return this.items.slice(this.startRow, endRow);
      }
      return [...this.items].sort(this.sort.bind(this)).slice(this.startRow, endRow);
    },
    itemMax() {
      const itemMax = this.startRow + this.rowsPerPage;
      if (itemMax > this.items.length) {
        return this.items.length;
      }
      return itemMax;
    },
  },
  methods: {
    isEqual(a, b) {
      return a === b;
    },
    selectAll() {
      if (this.value.length === this.items.length) {
        this.$emit('input', []);
      } else {
        this.$emit('input', this.items);
      }
    },
    selectItem(item) {
      const isInList = this.value.includes(item);
      const values = [...this.value];
      if (isInList) {
        const index = values.indexOf(item);
        values.splice(index, 1);
      } else {
        values.push(item);
      }
      this.$emit('input', values);
    },
    updateRows(rowPerPage) {
      this.$emit('update:rowsPerPage', rowPerPage);
    },
    scrollHorizontal(event) {
      const target = event.currentTarget;
      if (target.offsetWidth === target.scrollWidth) {
        return;
      }
      event.preventDefault();
      if (event.deltaX) {
        target.scrollLeft -= Math.round(event.deltaX / 4);
      }
      if (event.deltaY) {
        target.scrollLeft += Math.round(event.deltaY / 4);
      }
    },
    sortBy(sortKey) {
      if (this.sortKey === sortKey) {
        this.isAscending = !this.isAscending;
      } else {
        this.sortKey = sortKey;
        this.isAscending = true;
      }
    },
    pageUp() {
      this.startRow += this.rowsPerPage;
      this.$emit('pageUp');
    },
    pageDown() {
      this.startRow -= this.rowsPerPage;
      this.$emit('pageDown');
    },
  },
};
</script>
<style lang="scss" scoped>
@import '../styles/_elevation.scss';
@import '../styles/_variables.scss';

$selected-row-color: #d5e8f2;
$focused-row-color: #d5e8f2;

.vu-grid-view {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  border: 0.5px solid lightgray;

  .grid-view__container {
    width: 100%;
    order: 1;

    .grid-view__table {
      width: 100%;
      cursor: pointer;

      &:not(.dense) {
        tr {
          height: 38px;
        }
      }

      &--white-background tr {
        background-color: white;
      }

      .grid-view__table__checkbox {
        margin-bottom: 0px;
      }
      thead {
        border-bottom: 0.5px solid lightgray;
      }

      th {
        font-size: 14px;
        padding: 0.45455em;
      }

      tr,
      th,
      td {
        padding-left: 6px;
      }

      td.table {
        width: 10px;
      }

      .grid-view__table__body {
        tr {
          border-bottom: 0.5px solid lightgray;

          &:hover {
            background-color: #f4f5f6;
          }
        }
      }
    }
  }

  &--rich .grid-view__table {
    thead tr {
      background: $grey-3;
    }

    .grid-view__table__body {
      tr:nth-child(2n) {
        background: $grey-1;
      }

      tr:not(.selected):hover {
        background: $grey-4;
      }

      tr.selected {
        background: $selected-row-color;
        &:hover,
        &:focus {
          background: $focused-row-color;
        }
      }

      td.selected {
        border: 1px $blue-3;
      }
    }
  }
}

.grid-view__table__checkbox,
.grid-view__table__body__checkbox {
  visibility: hidden;
}

th:hover,
tr:hover,
.grid-view__table--has-selection {
  .grid-view__table__checkbox,
  .grid-view__table__body__checkbox {
    visibility: visible;
  }
}

.grid-view__pagination {
  display: flex;
  order: 2;
  width: 100%;

  &--top {
    order: 0;
  }

  align-self: end;
  flex-direction: row;
  align-items: center;
  justify-content: right;
  height: 50px;
  padding: 0 15px;
  gap: 5px;

  .form-group {
    margin-bottom: 0;
    min-width: 80px;
  }
}
</style>
