export const RegistrableForm = {
  data: () => ({
    inputs: [],
  }),
  methods: {
    register(input) {
      this.inputs.push(input);
    },
    unregister(input) {
      this.inputs = this.inputs
        .splice(this.inputs.indexOf(input), 1);
    },
    validate() {
      return this.inputs
        .map((el) => el.validate())
        .reduce((acc, el) => acc && el, true);
    },
  },
};

export const RegistrableInput = {
  computed: {
    form() {
      let parent = this.$parent;
      while (parent !== undefined && parent.$options._componentTag !== 'vu-form') {
        parent = parent.$parent;
      }
      return parent;
    },
  },
  created() {
    if (this.form) this.form.register(this);
  },
  beforeDelete() {
    if (this.form) this.form.unregister(this);
  },
};
