<template>
  <div class="vu-number form-group"
    :class="{ ...classes, 'vu-number--no-buttons': !showButtons }">
    <label class="control-label"
      v-if="label.length">{{ label }}<span v-if="required" class="label-field-required"> *</span></label>
    <div class="input-number">
      <button type="button"
        v-if="showButtons"
        :disabled="disabled"
        class="input-number-button input-number-button-left btn btn-default"
        @click="decrement">
      </button>
      <input v-bind="$attrs"
        ref="input"
        :value="value"
        :placeholder="placeholder"
        :disabled="disabled"
        :min="min"
        :max="max"
        :step="step"
        type="number"
        class="form-control"
        @keypress.up="increment"
        @keypress.down="decrement"
        @input="input($event.target.value, $event.data)">
      <button type="button"
        v-if="showButtons"
        :disabled="disabled"
        class="input-number-button input-number-button-right btn btn-default"
        @click="increment">
      </button>
    </div>
    <span v-for="(error, pos) in errorBucket"
      :key="`${pos}-error-${error}`"
      style="display: block"
      class="form-control-error-text">
      {{ error }}
    </span>
    <span v-if="helper.length"
      class="form-control-helper-text">{{ helper }}</span>
  </div>
</template>
<script>
import inputable from '../mixins/inputable';
import validatable from '../mixins/validatable';
import disablable from '../mixins/disablable';

import { RegistrableInput } from '../mixins/registrable';

export default {
  name: 'vu-input-number',
  inheritAttrs: false,
  mixins: [inputable, validatable, RegistrableInput, disablable],
  props: {
    step: {
      type: Number,
      default: () => 0.1,
    },
    decimal: {
      type: Number,
      default: () => 2,
    },
    min: {
      type: Number,
      default: 0,
    },
    max: {
      type: Number,
      default: Number.MAX_SAFE_INTEGER,
    },
    showButtons: {
      type: Boolean,
      default: true,
    },
  },
  methods: {
    input(v, added) {
      if (added && v === '' && this.value !== '') {
        // invalid input that corrupted the number
        this.$refs.input.value = this.value;
        return;
      }
      if ((v === '' && added === '-') || (added === '.' || added === ',')) return;
      this.$emit('input', v ? this.parseValue(this.fixed(v)) : '');
      this.$refs.input.value = this.value;
    },
    decrement() {
      let value = parseFloat(this.value);
      value = Number.isNaN(value) ? this.max : value;
      this.input((value - this.step));
    },
    increment() {
      let value = parseFloat(this.value);
      value = Number.isNaN(value) ? this.min : value;
      this.input((value + this.step));
    },
    parseValue(v) {
      const value = parseFloat(v);
      return value > this.max ? this.max : (value < this.min ? this.min : value);
    },
    fixed(v) {
      return Math.round(v * 10 ** this.decimal) / 10 ** this.decimal;
    },
  },
};
</script>
<style scoped lang="scss">
  @import '../styles/_variables.scss';

  .vu-number--no-buttons input {
    border: 1px solid $grey-5;
    border-radius: 4px;
  }

  input[type=number]::-webkit-inner-spin-button,
  input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  input[type=number] {
    -moz-appearance: textfield;
  }

  input.form-control {
    padding-left: 14px;
    padding-right: 14px;
  }
</style>
