<template>
  <div :class="['form-group', { dense }]">
    <label class="control-label" v-if="label.length"
      >{{ label }}<span v-if="required" class="label-field-required"> *</span></label>
    <div
      v-for="(option, index) in options"
      :key="`${_uid}-${option.value}-${index}`"
      class="toggle"
      :class="internalClasses">
      <input
        :type="type === 'radio' ? 'radio' : 'checkbox'"
        :id="`${_uid}-${option.value}-${index}`"
        :value="option.value"
        :disabled="disabled || option.disabled"
        :checked="isChecked(option.value)"
        :key="isChecked(option.value)"
        @click.prevent="input" />
      <label
        class="control-label"
        v-html="option.label"
        :for="`${_uid}-${option.value}-${index}`"
      >
      </label>
      <slot name="prepend-icon" v-bind:item="option" />
    </div>
    <span
      v-for="(error, pos) in errorBucket"
      :key="`${pos}-error-${error}`"
      style="display: block"
      class="form-control-error-text">
      {{ error }}
    </span>
    <span v-if="helper.length" class="form-control-helper-text">{{
      helper
    }}</span>
  </div>
</template>

<script>
import inputable from '../mixins/inputable';
import disablable from '../mixins/disablable';
import validatable from '../mixins/validatable';
import { RegistrableInput } from '../mixins/registrable';

export default {
  name: 'vu-checkbox',
  mixins: [inputable, validatable, RegistrableInput, disablable],
  inheritAttrs: false,
  props: {
    dense: {
      type: Boolean,
      default: () => false,
    },
    switch: {
      type: Boolean,
      required: false,
    },
    type: {
      type: String,
      default: () => 'checkbox',
    },
  },
  computed: {
    internalClasses() {
      return {
        'toggle-switch': this.type === 'switch',
        'toggle-primary': ['checkbox', 'radio', 'dense'].includes(this.type),
      };
    },
  },
  methods: {
    input(e) {
      if (this.options.length > 1 && this.type !== 'radio') {
        if (e.target.checked) {
          return this.$emit('input', [e.target.value].concat(this.value));
        }
        const result = JSON.parse(JSON.stringify(this.value));
        result.splice(this.value.indexOf(e.target.value), 1);
        return this.$emit('input', result);
      }
      return this.$emit('input', e.target.checked ? e.target.value : null);
    },
    isChecked(value) {
      if (Array.isArray(this.value)) return this.value.includes(value);
      return this.type === 'radio' ? this.value === value : !!this.value;
    },
  },
};
</script>

<style scoped lang="scss">
// Common
.toggle-primary + .control-label {
  padding-left: 10px;
}

.has-error label.control-label {
  color: initial;
}

  .dense {
    margin-bottom: 0;
    height: 19px;
    top: -5px;
    .control-label{
      top: -3px;
    }
    .toggle{
      margin-top: 4px;
      margin-bottom: 0;
    }
  }

.form-group:not(.dense) {
  .toggle:not(.toggle-sm) {
    min-height: 38px;

    &:not(.toggle-switch) {
      padding-left: 0;
    }

    & + .control-label {
      padding-left: 10px;
    }

    label {
      padding-top: 9px;
    }
  }

  .toggle:not(.toggle-sm) + .toggle:not(.toggle-sm) {
    margin-top: -11px;
  }

  // Checkbox and Radio
  .toggle-primary:not(.toggle-switch):not(.toggle-sm) .control-label {
    height: 100%;
    padding-right: 11px;
    padding-bottom: 8px;
  }

  .toggle-primary:not(.mixed):not(.toggle-switch):not(.toggle-sm)
    input[type="checkbox"]
    + .control-label {
    padding-left: 40px;

    &:before {
      top: 7px;
      left: 8px;
      height: 22px;
      width: 22px;
    }
    &:after {
      top: 5px;
      left: 9px;
      font-size: 20px;
    }
  }

  .toggle-primary:not(.toggle-switch):not(.toggle-sm)
    input[type="radio"]
    + .control-label {
    padding-left: 40px;

    &:before {
      top: 7px;
      left: 8px;
      width: 22px;
      height: 22px;
    }

    &:after {
      top: 12px;
      left: 13px;
      width: 12px;
      height: 12px;
    }
  }

  .prepend-icon {
    cursor: pointer;
    font-size: 18px;
    padding: 6px 8px;
    margin: 0;
  }

  .toggle-sm .prepend-icon {
    font-size: 14px;
    padding: 0 4px;
  }

  // Switch
  .toggle-switch:not(.toggle-sm) [type="checkbox"]:not(:checked) + .control-label,
  .toggle-switch:not(.toggle-sm) [type="checkbox"]:checked + .control-label {
    &:before {
      top: 6px;
    }
    &:after {
      top: 8px;
    }
  }

  .toggle-sm.toggle-switch [type="checkbox"]:not(:checked) + .control-label,
  .toggle-sm.toggle-switch [type="checkbox"]:checked + .control-label {
    padding-top: 4px;
    padding-left: 16px;
  }
}
</style>
