<template>
  <button
    type="button"
    v-mask="loading"
    :disabled="disabled"
    v-on="$listeners"
    :class="classes">
    <slot/>
  </button>
</template>

<script>
import loadable from '../mixins/loadable';
import activable from '../mixins/activable';
import colorable from '../mixins/colorable';
import inputable from '../mixins/inputable';
import disablable from '../mixins/disablable';

export default {
  name: 'vu-btn',
  mixins: [loadable, activable, colorable, inputable, disablable],
  props: {
    large: {
      type: Boolean,
      default: () => false,
    },
    small: {
      type: Boolean,
      default: () => false,
    },
    block: {
      type: Boolean,
      default: () => false,
    },
  },
  computed: {
    classes() {
      return [`btn btn-${this.color}`,
        {
          'btn-sm': this.small,
          'btn-lg': this.large,
          'btn-block': this.block,
          active: this.active,
        },
      ];
    },
  },
};
</script>

