<template>
  <span
    :class="classes"
    @click="(e) => selectBadge(e)"
    v-click-outside="{
      handler: onClickOutside,
      events: ['click'],
    }"
  >
    <span v-if="icon" :class="iconClasses" /><span class="badge-content">
      <slot />
    </span><span v-if="closable"
      class="fonticon fonticon-cancel"
      v-on:click="$emit('close')"
    />
  </span>
</template>
<script>
import colorable from '../mixins/colorable';
import disablable from '../mixins/disablable';

export default {
  name: 'vu-badge',
  mixins: [colorable, disablable],
  props: {
    value: {
      type: Boolean,
      default: () => undefined,
    },
    icon: {
      type: String,
      default: () => '',
    },
    selectable: {
      type: Boolean,
      default: () => false,
    },
    togglable: {
      type: Boolean,
      default: () => true,
    },
    closable: {
      type: Boolean,
      default: () => false,
    },
  },
  data() {
    return {
      isSelected: false,
    };
  },
  computed: {
    classes() {
      return [
        `badge-root badge badge-${this.color}`,
        {
          'badge-closable': this.closable,
          'badge-selectable': this.selectable,
          disabled: this.disabled,
          'badge-selected': this.isSelected || this.value,
        },
      ];
    },
    iconClasses() {
      return `fonticon fonticon-${this.icon} badge-icon`;
    },
  },
  methods: {
    onClickOutside() {
      if (!this.selectable) { return; }

      if (this.value === undefined) {
        if (this.togglable) {
          this.isSelected = false;
        }
      }
    },
    selectBadge() {
      if (!this.selectable) { return; }

      if (this.value === undefined) {
        this.isSelected = this.togglable ? !this.isSelected : true;
      }
      this.$emit('selected', this.isSelected);
      this.$emit('input', this.isSelected);
    },
  },
};
</script>
