const actionsMerge = (actions, base, customized) => {
  let array = actions;
  if (!customized) {
    array = actions.slice(0, base.length).filter(({ name }) => base.find(({ name: baseName }) => (name === baseName)));
    array = array.map((val) => ({
      ...base.find(({ name }) => (val.name === name)),
      ...val,
    }));
  }
  return array;
};

const actionsMergeSubs = (base, actions) => {
  const common = actions.filter(({ name }) => base.find(({ name: baseName }) => (name === baseName)));
  const remaining = actions.filter(({ name }) => !common.find(({ name: baseName }) => (name === baseName)));
  base.forEach(({ name: elementName, items }) => {
    const action = common.find(({ name }) => name === elementName);
    if (action) {
      const { items: actionItems } = action;
      if (actionItems) {
        if (!Array.isArray(items)) {
          // eslint-disable-next-line no-param-reassign
          items = [];
        }
        items.push(...actionItems);
      }
    }
  });
  const array = [...base, ...remaining];
  return array;
};

export default actionsMerge;
export {
  actionsMerge,
  actionsMergeSubs,
};
