const isValidDate = (obj) => obj instanceof Date && !Number.isNaN(obj.getTime());

// Solution by Matti Virkkunen: http://stackoverflow.com/a/4881951
const isLeapYear = (year) => ((year % 4) === 0 && year % 100 !== 0) || year % 400 === 0;

const getDaysInMonth = (year, month) => [31, isLeapYear(year) ? 29 : 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31][month];

const setToStartOfDay = (date) => {
  if (isValidDate(date)) date.setHours(0, 0, 0, 0);
};

// Weak date comparison (use setToStartOfDay(date) to ensure correct result)
const compareDates = (a, b) => a.getTime() === b.getTime();

const parse = (a) => {
  let value;
  if (isValidDate(a)) {
    value = a;
  } else if (a && typeof a === 'string') {
    try {
      value = new Date(Date.parse(a));
    } catch {} // eslint-disable-line no-empty
  }
  return value;
};

export {
  isValidDate,
  isLeapYear,
  getDaysInMonth,
  setToStartOfDay,
  compareDates,
  parse,
};
