// TODO TS extend Inputable.

export default {
  props: {
    value: [Array, Number, Date],
    min: {
      type: [Number, Date],
      default: () => -2208988800000, // 1900-01-01Z00:00:00.000Z
    },
    max: {
      type: [Number, Date],
      default: () => 4102444799999, // 2099-12-31T23:59:59.999Z
    },
  },
  watch: {
    min: {
      handler(v) {
        this.checkBoundary(v, 'min');
      },
      immediate: true,
    },
    max: {
      handler(v) {
        this.checkBoundary(v, 'max');
      },
      immediate: true,
    },
  },
  methods: {
    checkBoundary(value, boundary) {
      if (!this.value) return;

      const event = this.$listeners['boundary-change'] ? 'boundary-change' : 'input';
      if (
        (['min'].includes(boundary) && this.value < value)
        || (['max'].includes(boundary) && this.value > value)
      ) {
        if (this.$options.propsData.value instanceof Date) { // we are manipulating a date, we must input a date
          this.$emit(event, event === 'input' ? new Date(value) : { boundary, value: new Date(value) });
        } else {
          this.$emit(event, event === 'input' ? value : { boundary, value });
        }
      }
    },
  },
};
