import LightBox from './vu-lightbox.vue';
import dasherize from '../../utils/dasherize';

let lightboxInstance;
let LightboxConstructor;

const install = (Vue) => {
  LightboxConstructor = Vue.extend(LightBox);
};

const lightbox = (options) => {
  if (!lightboxInstance) {
    lightboxInstance = new LightboxConstructor();
  } else {
    // unsubscribe off other events.
    lightboxInstance.$delete(lightboxInstance.$listeners);
    lightboxInstance.$off();
  }

  const values = { ...options };
  const props = lightboxInstance.$props;

  lightboxInstance.$on('update:show', (val) => {
    props.show = val;
  });

  const bindings = Object.keys(values).filter((v) => !v.startsWith('on'));
  const events = Object.entries(values).filter(([key]) => key.startsWith('on'));
  bindings.forEach((key) => {
    props[key] = values[key];
  });
  events.forEach(([key, value]) => {
    const eventName = dasherize(key.substring(2)).replace('-', ':');
    lightboxInstance.$on(eventName, (event) => { value.apply(event); });
  });

  // Show
  if (values.show === false || values.show === undefined) { props.show = true; }

  // Mount
  if (!lightboxInstance._isMounted) {
    lightboxInstance.$mount();
    document.body.insertBefore(lightboxInstance.$el, document.body.childNodes[0]);
  }

  return lightboxInstance;
};

const show = () => {
  lightboxInstance.$props.show = true;
};

export default {
  install,
  lightbox,
  show,
};
