<template>
  <div
    class="alert alert-root"
    style="visibility: visible;"
  >
   <vu-message
      v-for="message in messages"
      v-bind="message"
      :key="`message-${message.uid}`"
      @update:show="hide(message)"
   />
  </div>
</template>

<script>

export default {
  name: 'vu-message-wrapper',
  data: () => ({
    messages: [],
  }),
  methods: {
    add(options) {
      const uid = Date.now();

      // eslint-disable-next-line no-param-reassign
      options = typeof options === 'object' ? options : { title: options };

      const message = {
        uid,
        show: true,
        ...options,
      };
      this.messages.push(message);
      return this.hide.bind(this, message);
    },
    hide(message) {
      // eslint-disable-next-line no-param-reassign
      message.show = false;
      const i = this.messages.indexOf(message);
      if (i === -1) return;
      this.messages.splice(i, 1);
    },
  },
};
</script>
<style scoped lang="css">
.alert-fade-enter, .alert-fade-leave-to {
  opacity: 0;
}

.alert-fade-enter-active, .alert-fade-leave-active {
  transition: all .5s ease-in-out;
}
</style>
