/* eslint-disable func-names */
const isArray = Array.isArray || function (obj) {
  return Object.prototype.toString.call(obj) === '[object Array]';
};

const isDate = function (obj) {
  return Object.prototype.toString.call(obj) === '[object Date]';
};

const isRegex = function (obj) {
  return Object.prototype.toString.call(obj) === '[object RegExp]';
};

const has = Object.prototype.hasOwnProperty;
const objectKeys = Object.keys || function (obj) {
  const keys = [];
  // eslint-disable-next-line no-restricted-syntax
  for (const key in obj) {
    if (has.call(obj, key)) {
      keys.push(key);
    }
  }
  return keys;
};

function dashCase(str) {
  return str.replace(/[A-Z](?:(?=[^A-Z])|[A-Z]*(?=[A-Z][^A-Z]|$))/g, (s, i) => (i > 0 ? '-' : '') + s.toLowerCase());
}

function map(xs, f) {
  if (xs.map) {
    return xs.map(f);
  }
  const res = [];
  for (let i = 0; i < xs.length; i++) {
    res.push(f(xs[i], i));
  }
  return res;
}

function reduce(xs, f, acc) {
  if (xs.reduce) {
    return xs.reduce(f, acc);
  }
  for (let i = 0; i < xs.length; i++) {
    // eslint-disable-next-line no-param-reassign
    acc = f(acc, xs[i], i);
  }
  return acc;
}

function walk(obj) {
  if (!obj || typeof obj !== 'object') {
    return obj;
  }
  if (isDate(obj) || isRegex(obj)) {
    return obj;
  }
  if (isArray(obj)) {
    return map(obj, walk);
  }
  return reduce(objectKeys(obj), (acc, key) => {
    const camel = dashCase(key);
    acc[camel] = walk(obj[key]);
    return acc;
  }, {});
}

module.exports = function (obj) {
  if (typeof obj === 'string') {
    const str = obj.replaceAll(' ', '-');
    return dashCase(str);
  }
  return walk(obj);
};
