<template>
  <div v-if="keepRendered || isActive" v-show="isActive">
    <div
      class="modal modal-root vuekit-modal"
      style="display: block;"
    >
      <div class="modal-wrap">
        <div class="modal-content">
          <div class="modal-header">
            <slot name="modal-header">
              <span v-if="showCancelIcon"
                @click="cancel(true)"
                class="close fonticon fonticon-cancel"
                title=""
              />
              <h4>{{ title }}</h4>
            </slot>
          </div>
          <div class="modal-body">
            <slot name="modal-body">
              <div v-if="rawContent" v-html="rawContent" />
              <p v-if="!showInput || message">
                {{ message }}
              </p>
              <vu-form ref="form"
                v-if="showInput">
                <vu-input
                  :label="label"
                  :required="required"
                  :helper="helper"
                  :success="success"
                  :placeholder="placeholder"
                  :rules="rules"
                  v-model="model" />
              </vu-form>
            </slot>
          </div>
          <div v-if="showFooter" class="modal-footer">
            <slot name="modal-footer">
              <vu-btn
                color="primary"
                @click="confirm">{{ okLabel }}</vu-btn>
              <vu-btn v-if="showCancelButton"
                color="default"
                @click="cancel()">{{ cancelLabel }}</vu-btn>
            </slot>
          </div>
        </div>
      </div>
    </div>
    <div class="modal-overlay in" />
  </div>
</template>
<script>
import showable from '../../mixins/showable';

export default {
  name: 'vu-modal',
  data: () => ({
    model: '',
  }),
  mixins: [showable],
  props: {
    show: {
      type: Boolean,
      required: false,
      default: () => false,
    },
    keepRendered: {
      type: Boolean,
      default: () => false,
    },
    title: {
      type: String,
      default: () => '',
    },
    message: {
      type: String,
      default: () => '',
    },
    rawContent: {
      type: String,
      default: '',
    },
    showCancelIcon: {
      type: Boolean,
      default: () => true,
    },
    showCancelButton: {
      type: Boolean,
      default: () => false,
    },
    showFooter: {
      type: Boolean,
      default: () => true,
    },
    showInput: {
      type: Boolean,
      default: () => false,
    },
    /* input props */
    label: {
      type: String,
      default: () => '',
    },
    helper: {
      type: String,
      default: () => '',
    },
    placeholder: {
      type: Boolean,
      default: () => true,
    },
    color: {
      type: String,
      default: () => '',
    },
    rules: {
      type: Array,
      default: () => [],
    },
    required: {
      type: Boolean,
      default: () => true,
    },
    success: {
      type: Boolean,
      default: () => false,
    },
    /* input props */
    cancelLabel: {
      type: String,
      default: () => 'Cancel',
    },
    okLabel: {
      type: String,
      default: () => 'OK',
    },
  },
  methods: {
    cancel(fromIcon = false) {
      this.isActive = false;
      this.$emit(fromIcon ? 'close' : 'cancel');
      if (this.showInput) this.clear();
    },
    confirm() {
      if (!this.showInput) {
        this.$emit('confirm', true);
      } else if (this.$refs.form.validate()) {
        this.$emit('confirm', this.model);
        this.clear();
      }
    },
    clear() {
      this.model = '';
    },
  },
};
</script>
<style>
.vuekit-modal .form-group {
  margin-bottom: 0px;
}
</style>
